////
/// Content
/// @group base
////

html {
    @include font-family(default);
    font-size: $base-font-size;
    font-style: normal;
    font-weight: $font-weight-default;
    line-height: $line-height-default;
    font-feature-settings: 'lnum' 1;
}

/**
 * Content Elements
 */
p,
ul,
ol,
dl,
figure,
table,
blockquote {
    @extend %content-elements !optional;
}

/**
 * Heading Elements
 */
h1,
h2,
h3,
h4,
h5,
h6 {
    @extend %heading-elements !optional;
    @include font-family(meta);
    width: 100%;
    display: block;
    flex: 1 0 auto;
    position: relative;
    margin-bottom: $measure-medium;
    font-weight: $font-weight-default;
}

.alpha,
%alpha {
    @include typographic-scale($font-size-alpha);
}
h1 {
    @extend %alpha !optional;
    font-weight: $font-weight-medium;
}

.beta,
%beta {
    @include typographic-scale($font-size-beta);
}
h2 {
    @extend %beta !optional;
    font-weight: $font-weight-medium;
}

.gamma,
%gamma {
    @include typographic-scale($font-size-gamma);
}
h3 {
    @extend %gamma !optional;
    font-weight: $font-weight-medium;
}

.delta,
%delta {
    @include typographic-scale($font-size-delta);
}
h4 {
    @extend %delta !optional;
    font-weight: $font-weight-medium;
}

.epsilon,
%epsilon {
    @include typographic-scale($font-size-default);
}
h5 {
    @extend %epsilon !optional;
    font-weight: $font-weight-bold;
}

.zeta,
%zeta {
    @include typographic-scale($font-size-default);
}
h6 {
    @extend %zeta !optional;
    font-weight: $font-weight-bold;
}

p {
    margin: 0 0 #{$line-height-default}em;

    &:empty {
        display: none;
        visibility: hidden;
    }

    &.columns {
        @include media('>large') {
            text-align: justify;
            column-count: 2;
            column-gap: $measure-large-relative;
        }
    }
}

/**
 * Lists
 */
ol,
ul {
    padding: 0;
    margin: 0;
    list-style: none;
}
ol:not([class]),
ul:not([class]),
.default-list {
    list-style-position: outside;
    margin-bottom: $measure-medium;
    margin-left: $measure-medium;

    // List nested in a list
    & & {
        margin-top: $measure-medium;
    }

    li + li {
        margin-top: 0.5em;
    }
}
ul:not([class]),
ul.default-list {
    list-style-type: square;
}
ol:not([class]),
ol.default-list {
    list-style-type: decimal;

    &[reversed] {
        display: flex;
        flex-direction: column-reverse;
    }
}

figure {
    max-width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 0 0 $measure-medium;

    figcaption {
        @include font-family(serif);
        display: flex;
        flex-direction: column;
        align-items: center;
        padding-top: $measure-medium;
        font-style: italic;
        font-size: $font-size-small;
        line-height: $line-height-small;
    }

    & > a {
        display: block;
    }

    img {
        width: 100%;
    }
}

/**
 * Quotes
 */
blockquote {
    @include quotes;

    p {
        &:not(:last-of-type) {
            &:after {
                @include visually-hidden;
            }
        }
    }
}

small {
    font-size: $font-size-small;
}

sub,
sup {
    font-size: 0.4em;
    position: relative;
    vertical-align: baseline;
    line-height: 0;
}

sup {
    top: -0.6em;
}

sub {
    bottom: -0.25em;
}
/**
 * Neuter bad editors
 **/
a,
h1,
h2,
h3,
h4,
h5,
h6 {
    b,
    strong {
        font-weight: inherit;
    }
}

iframe[src*='prezi'] {
    display: none !important;
}
