.feature-events {
    margin: 30px 0;
    position: relative;

    @include media('>medium') {
        margin-bottom: 100px;
    }
}

.feature-events__cards__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    position: relative;

    .feature-events__cards__content{
        margin-bottom: $measure-large;

        @include media('<large'){
            width: 50%;
        }

        @include media('<medium'){
            width: 100%;
            
            .featured-event{
                padding: 0;
            }
        }
    }

    .featured-item__content{
        width: 100%;
        top: 0;
    }
}

.feature-events__cards__content {
    @include grid-column(4);
}

.feature-events__cards__notice-type {
    width: 100%;

    .featured-item__content{
        width: 90%;
    }

    time {
        font-size: $measure-large;
    }
}

.feature-events__cards__content__item {
    box-shadow: 0 0 1.5rem 3px rgba(0, 0, 0, 0.15);
    padding: $measure-large;

    @include media('<=medium') {
        margin: $measure-medium 0;
    }

    @include media('>medium') {
        margin: 0 $measure-medium;
        height: 100%;
    }

    time {
        display: block;
        margin-bottom: 20px;
        font-size: $font-size-small;
        font-weight: 100;
    }
}

.feature-events__cards__link {
    a {
        @include anchor(skate);
        margin-bottom: 10px;
    }
    position: absolute;
    bottom: -60px;
    right: 40px;
    @include media('>large') {
        bottom: -100px;
    }
}

.feature-events__cards__content__item__title {
    a {
        @include anchor(skate);

        font-family: $font-family-meta;
        font-size: $font-size-delta;
        font-weight: $font-weight-medium;
    }
}
// possibly delete after push?
.feature-events__cards__content__item__audience {
    a {
        @include brackets();
        font-size: $font-size-small;
    }
}

[data-event-audience*='Prospective Student Events'] {
    a {
        @include brackets($cass-prospective);
    }
}
[data-event-audience*='Executive Education'] {
    a {
        @include brackets($entrepreneurship);
    }
}
[data-event-audience*='Cass Society'] {
    a {
        @include brackets($cass-society);
    }
}
[data-event-audience*='Professional and Careers'] {
    a {
        @include brackets($cass-careers);
    }
}
[data-event-audience*='Faculty/Research'] {
    a {
        @include brackets($cass-faculty);
    }
}
[data-event-audience*='Corporate'] {
    a {
        @include brackets($cass-corporate);
    }
}
[data-event-audience*='Alumni'] {
    a {
        @include brackets($cass-alumni);
    }
}
