$quote-themes: (
    corporate: (
        $color-alizarin,
    ),
    undergraduate: (
        $color-flamingo,
    ),
    masters: (
        $color-prussian,
    ),
    phd: (
        $color-allports,
    ),
    mba: (
        $color-mba-lead,
    ),
    online-msc: (
        $color-omba-lead,
    ),
    emba: (
        $color-emba-lead,
    ),
    demba: (
        $color-demba-lead,
    ),
    cce: (
        $color-cce-lead,
    ),
    executive-education: (
        $color-vivid,
    ),
);

@each $theme, $colors in $quote-themes {
    $quote-color: nth($colors, 1);

    .scp--#{$theme} {
        blockquote {
            &::before {
                background-color: $quote-color;
            }
        }
    }
}

blockquote {
    padding: $measure-large $measure-large $measure-large $measure-large;
    position: relative;

    &::before {
        font-family: $font-family-meta;
        content: ('');
        text-align: center;
        margin-left: auto;
        margin-right: auto;
        display: block;
        margin-bottom: $measure-large;
        width: 180px;
        height: 40px;
        background-color: black;
        mask: url('../img/quote.svg') no-repeat;
        mask-size: 100%;
    }

    & p {
        font-family: $font-family-meta;
        font-size: $font-size-delta;
    }

    & .reference {
        color: $color-nobel;
        font-size: $font-size-default;
    }
}

@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
    /* IE10+ CSS styles go here */
    blockquote::before {
        content: url('../img/quote.svg') !important;
        background-color: transparent !important;
    }
}

/* IE Edge 12+ CSS styles go here */
@supports (-ms-ime-align: auto) {
    blockquote::before {
        content: url('../img/quote.svg') !important;
        background-color: transparent !important;
    }
}
