////
/// Footer
/// @group layout
////

.footer {
    background-color: $color-white;
    padding-bottom: $measure-enormous;
    border-top: $border-style-default $color-gallery $border-width-outline;
}

.footer__back-to-top {
    width: 100%;
    padding-bottom: $measure-small;

    a {
        float: right;
    }

    span:not([class]) {
        @include visually-hidden;
    }
}

.footer__section {
    padding-top: $measure-medium;
    padding-bottom: $measure-medium;
    text-align: center;

    @include media('>medium') {
        text-align: left;
    }
}

.footer__section--second {
    border-top: $border-style-default $color-lightgray $border-width-outline;
    padding-top: $measure-large;
}

.footer__address {
    margin-bottom: $measure-medium;
    font-style: normal;
}

.footer__address-organisation {
    margin-bottom: $measure-small;
    font-size: $font-size-gamma;
    font-weight: $font-weight-medium;
}

.footer__address-details {
    display: block;
    font-size: $font-size-small;

    span {
        display: block;
        margin-bottom: $measure-small;
    }

    .footer__address-phone {
        @include media('>medium') {
            margin-top: $measure-large;
        }
    }

    a {
        @include anchor(skate);
    }
}

.footer__misc-links {
    margin-bottom: $measure-large;
    font-size: $font-size-small;

    li {
        display: inline;

        @include media('>medium') {
            display: block;
        }
    }

    li + li {
        @include media('<=medium') {
            margin-left: $measure-medium;
        }
    }

    a {
        @include anchor(skate);
    }
}

.footer__social {
    li {
        display: inline-block;
        margin-bottom: $measure-small;

        @include media('>medium') {
            display: block;
        }
    }

    li + li {
        @include media('<=medium') {
            margin-left: $measure-medium;
        }
    }

    a {
        @include anchor(simple-image);
        color: $color-lucifer;

        @include media('>medium') {
            @include anchor(skate);
        }
    }

    .fa {
        font-size: $font-size-gamma;
        vertical-align: top;
    }

    span:not([class]) {
        font-size: $font-size-small;
        margin-left: $measure-medium;
        vertical-align: middle;

        @include media('<=medium') {
            @include visually-hidden;
        }
    }
}

.footer__social-heading {
    display: block;
    margin-bottom: $measure-small;
    font-weight: $font-weight-medium;

    @include media('>medium') {
        @include visually-hidden;
    }
}

.footer__accreditation,
.footer__bloomberg {
    a {
        @include anchor(simple-image);
        display: inline-block;
        margin-right: $measure-medium;
        margin-bottom: $measure-small;
    }

    img {
        height: 35px;
    }
}

.footer__accreditation-title {
    display: block;
    margin-bottom: $measure-medium;
    font-weight: $font-weight-medium;

    @include media('>medium') {
        display: inline-block;
    }
}

.footer__copyright {
    font-size: $font-size-small;

    span {
        display: block;
    }
}

.footer__promo-box{
    position: relative;
    padding: $measure-xlarge $measure-medium;
    background-color: $ribbon-color;
    color: $color-white;  

    h2{
        @include typographic-scale($font-size-gamma);
    }

    a{ 
        @include anchor-reset();
        text-decoration: underline;
        color: $color-white;

        &:focus{
            outline: $border-width-default $border-style-default $color-white;
        }

        &:active{
            outline: $border-width-default $border-style-alternate $color-white;
        }
    }

    @include media('>large'){
        margin-right: $measure-xlarge;
    }

    @include media('>medium'){
        top: -40%;
    }

    @include media('<=medium'){
        position: absolute;
        margin: 0 $measure-xlarge;
        top: -$footer-promo-box-offset-top;
    }
}
