.cass-knowledge-listing{
    .container__more-news{
        @include media('>extralarge'){
            margin-top: $measure-medium;
        }
    }

}

.cass-knowledge-listing__item {
    .news__image-credit {
        display: flex;
        z-index: 1;
        padding: .2em;
        top: 0;
        left: 1em;
        right: unset;
        bottom: unset;
    }

    .image-credit__text {
        order: 2;
        padding: 0 .35em .35em .35em;

        a {
            padding-bottom: .2em;
            font-weight: 600;
        }
    }

    .image-credit__button {
        padding: .4em .6em;
        float: none;
        order: 1;
    }
}

.cass-knowledge-listing__grid {
    @extend .grid;

    > div {
        @include grid-column(4);
        // fixes layout issues in IE:
        display: inline-block;
        vertical-align: top;
        float: none !important;
    }

    &.cass-knowledge-listing__grid--1 {
        > div {
            @include grid-column(6);
            @include grid-column-push(3);
        }
    }

    &.cass-knowledge-listing__grid--2 {
        > div {
            @include grid-column(6);
            
        }
    }
}

.cass-knowledge-listing__link {
    text-align: right;
    margin-top: $measure-enormous;
    font-size: $font-size-small;
    font-weight: 600;

    
    a {
        @include anchor(skate);
    }

    .fa {
        margin-left: $measure-large;
    }
}

.related-stories-section .landscape-knowledge-cards 
    .cass-knowledge-listing__grid.cass-knowledge-listing__grid--2 > div {

    

   @include media('>large'){
        min-height: 25rem; 
        margin-bottom: $measure-large;
        
   }

   @include media('>extralarge'){
    padding-bottom: 2%;
    overflow: hidden;
    min-height: auto;
    
    }

}

.cass-knowledge-listing__item {
    position: relative;
    margin-bottom: $measure-large;
}

@mixin absolute-i {
    position: absolute;
    display: inline-block;
}

@mixin relative-i {
    position: relative;
    display: block;
}

@mixin relative-ii {
    position: relative;
    display: inline-block;
    float: right;
}

$cass-knowledge-breakpoints: (
    ('min', 100%, 100%, 1),
    ('small', 90%, 60%, 2),
    ('between', 65%, 50%, 2),
    ('medium', 100%, 100%, 1),
    ('large', 95%, 55%, 3),
    ('extralarge', 75%, 45%, 3)
);

@each $bp in $cass-knowledge-breakpoints {
    @include media('>#{nth($bp, 1)}') {
        .cass-knowledge-listing__item img {
            width: nth($bp, 2);
        }
        .cass-knowledge-listing__item__text {
            width: nth($bp, 3);
        }

        @if (nth($bp, 4) == 3) {
            .cass-knowledge-listing__item img {
                @include relative-i;
            }
            .cass-knowledge-listing__item__text {
                @include absolute-i;
            }
        } @else if (nth($bp, 4) == 2) {
            .cass-knowledge-listing__item img {
                @include relative-i;
            }
            .cass-knowledge-listing__item__text {
                @include absolute-i;
            }
        } @else {
            .cass-knowledge-listing__item img {
                @include relative-i;
            }
            .cass-knowledge-listing__item__text {
                @include relative-i;
            }
        }
    }
}

.cass-knowledge-listing__item__text {
    background-color: white;
    top: 0;
    right: 0;
    padding: $measure-medium $measure-large $measure-large;
    box-shadow: 0 0 1.5rem 3px rgba(0, 0, 0, 0.3);

    a {
        
        font-size: $font-size-delta;
        font-weight: $font-weight-medium;
        border-bottom: none;

        
    }

    > a {
        margin-bottom: $measure-large;
    }
}

.cass-knowledge-listing__item__title{
    font-family: $font-family-meta;
    @include multi-line-animation;
}

.cass-knowledge-listing__item__date{
    font-weight: $font-weight-medium;
    font-size: $font-size-small;
    color: $color-lucifer;

}

.cass-knowledge-listing__item__topic {

    $topics: (
        (actuarial-science $actuarial-science),
        (all $color-lucifer),
        (finance $finance),
        (management $management),
        (entrepreneurship $entrepreneurship)
    );

    @each $topic in $topics {
        &.cass-knowledge-listing__item__topic--#{nth($topic, 1)} {
            @include anchor-border-float-in(nth($topic, 2));
            @include news-article-anchor(nth($topic, 2));
        }
    }

    a {
        font-size: $font-size-small;
        &:hover,&:focus{
            background: none;
            border-bottom:none;
        }
    }
}

.cass-knowledge-listing__item__research {
    position: relative;
    color: $color-emperor;
    margin-bottom: $measure-large;
    text-transform: uppercase;
    font-size: $font-size-small;
    font-weight: 600;

    &:before {
        position: absolute;
        content: '';
        top: 0;
        left: -$measure-large;
        width: 5px;
        height: 100%;
        background: black;
    }
}
