////
/// Stats Block
/// @group components
////

.stats-block {
    color: $color-white;
    text-align: center;
    width: 100%;

    h2 {
        padding: $measure-medium 0;
        margin-bottom: 0;
        border-bottom: $border-width-hairline $border-style-default
            $color-flamingo;
        font-size: $font-size-default;
    }
}

.stats-block.stats-block--prussian {
    background-image: linear-gradient(
        to bottom,
        $color-prussian,
        $color-allports
    );
    padding-left: $measure-medium;
    padding-right: $measure-medium;
    overflow: auto;

    @include media('<=small') {
        margin-top: $measure-large;
    }

    h2 {
        border-bottom: $border-width-hairline $border-style-default
            $color-allports;
    }
}

.stats-block.stats-block--orange {
    background-image: linear-gradient(to bottom, $color-yellow, $color-orange);
    padding-left: $measure-medium;
    padding-right: $measure-medium;
    overflow: auto;
    color: black;

    @include media('<small') {
        margin-top: $measure-large;
    }

    h2 {
        margin: 0 5%;
        width: auto;
        border-bottom: $border-width-hairline $border-style-default
            $color-orange;
    }

    .stats-block__piechart .stats-block__piechart__value {
        stroke: $color-black;
    }

    .stats-block__piechart .stats-block__piechart__base {
        fill: none;
        stroke: black;
        stroke-opacity: 0.5;
    }

    .stats-block__item {
        margin: $measure-large 0;
        @include grid-column(4);

        @include media('>small') {
            &:nth-child(1),
            &:nth-child(2) {
                border-right: 1px solid $color-orange;
            }
            @include grid-column(4);
        }
    }
}

.stats-block__grid {
    @include grid;
    padding-bottom: $measure-small;

    & > div {
        align-self: center;
    }
}

.stats-block__grid--dlhe {
    & > :nth-child(1) {
        @include grid-column-push(1);
        @include grid-column(2);
    }

    & > :nth-child(2) {
        @include grid-column(4);
        @include grid-column-push(2);
    }

    & > :nth-child(3) {
        @include grid-column-push(3);
        @include grid-column(2);
    }
}

.stats-block__grid--usp2c:not(.stats-block__grid--dlhe) {
    & > div {
        @include grid-column-push(2);
        @include grid-column(4);
    }
}

.stats-block__grid:not(.stats-block__grid--usp2c):not(.stats-block__grid--dlhe) {
    & > div {
        @include grid-column-push(4);
        @include grid-column(4);
    }
}

.stats-block__grid--halves {
    @extend .grid--halves;
}

.stats-block__item {
    @include font-family(meta);
    text-align: center;
    padding-bottom: $measure-large;
    padding-top: $measure-large;
}

.stats-block__item__value {
    font-size: 2em;
    font-weight: $font-weight-light;
}

.stats-block__item__icon {
    img {
        width: 100%;
        max-width: 80px;
        display: inline-block;
    }
}

.stats-block__piechart {
    width: 100%;
    display: flex;
    justify-content: center;
    text-align: center;
    margin-bottom: $measure-small;
    position: relative;

    .stats-block__piechart__base {
        fill: none;
        stroke: #757373;
    }

    .stats-block__piechart__value {
        fill: none;
        stroke: $color-quill;

        @extend %animate--svg-path-target;
    }

    .stats-block__piechart__text {
        @include absolute(top);
        @include font-family(meta);

        top: 50%;
        margin-top: -0.75em; // MAGIC NUMBER OVERRIDE ALERT
        font-size: $font-size-alpha;
        display: block;
        width: 100%;
        text-align: center;
    }

    svg {
        width: 100%;
        max-width: 150px;
        display: inline-block;
    }
}

.stats-block.stats-block--scp {
    padding-left: $measure-medium;
    padding-right: $measure-medium;
    overflow: auto;
    margin-top: $measure-medium;
    margin-bottom: $measure-medium;

    @include media('<=small') {
        margin-top: $measure-large;
    }

    .stats-block__grid {
        justify-content: center;

        .stats-block__item {
            left: 0;
        }

        .stats-block__item__icon {
            svg {
                width: 100%;
                max-width: 80px;
                display: inline-block;
                fill: $color-white;
            }
        }

        &[data-count='1'] {
            .stats-block__item {
                @include media('>medium') {
                    flex-basis: 50%;
                }
            }
        }

        &[data-count='2'] {
            .stats-block__item {
                @include media('>medium') {
                    flex-basis: 50%;
                }
            }
        }

        &[data-count='3'] {
            .stats-block__item {
                @include media('>medium') {
                    flex-basis: 33.33%;
                }
            }
        }
        &[data-count='4'] {
            .stats-block__item {
                @include media('>medium') {
                    flex-basis: 50%;
                }
            }
        }
    }
}

.scp--undergraduate .stats-block.stats-block--scp,
.stats-block--scp.stats-block--undergraduate {
    @include stats-style(dark);
    background-image: linear-gradient(
        to bottom,
        $color-yellow,
        $color-flamingo
    );

    h2 {
        border-bottom: $border-width-hairline $border-style-default
            $color-flamingo;
    }
}

.scp--masters .stats-block.stats-block--scp,
.stats-block--scp.stats-block--postgraduate {
    @include stats-style(light);

    background-image: linear-gradient(
        to bottom,
        $color-prussian,
        $color-allports
    );

    h2 {
        border-bottom: $border-width-hairline $border-style-default
            $color-allports;
    }
}

.scp--mba .stats-block.stats-block--scp,
.stats-block--scp.stats-block--mba {
    @include stats-style(dark);
    background-image: linear-gradient(
        to bottom,
        $color-seablue,
        $color-montecarlo
    );

    h2 {
        border-bottom: $border-width-hairline $border-style-default
            $color-sherpa;
    }
}

.scp--phd .stats-block.stats-block--scp,
.stats-block--scp.stats-block--phd {
    @include stats-style(light);

    background-image: linear-gradient(
        to bottom,
        $color-darkteal,
        $color-allports
    );

    h2 {
        border-bottom: $border-width-hairline $border-style-default
            $color-picton;
    }
}

.scp--executive-education .stats-block.stats-block--scp,
.stats-block--scp.stats-block--execed {
    @include stats-style(light);
    background-image: linear-gradient(
        to bottom,
        $color-vivid,
        $color-clairvoyante
    );

    h2 {
        border-bottom: $border-width-hairline $border-style-default
            $color-violet;
    }
}

.scp--corporate .stats-block.stats-block--scp,
.stats-block--scp.stats-block--corporate {
    @include stats-style(light);
    background-image: linear-gradient(
        to bottom,
        $ribbon-color,
        $color-alizarin
    );

    h2 {
        border-bottom: $border-width-hairline $border-style-default
            $color-alizarin;
    }
}
