.banner {
    position: relative;
}

.banner__container {
    @include media('>between'){
        @include grid-top;

        & + & {
            margin-top: $measure-large;
        }
    }
}

$banner-themes: (
        corporate: (
                $color-alizarin,
                $color-white,
                $color-lead-mid-grey,
                $color-white
        ),
        undergraduate: (
                $color-ug-lead,
                $color-black,
                $color-ug-accent,
                $color-black
        ),
        postgraduate: (
                $color-prussian,
                $color-white,
                $color-allports,
                $color-white
        ),
        masters: (
                $color-prussian,
                $color-white,
                $color-allports,
                $color-white
        ),
        phd: (
                $color-phd-lead,
                $color-white,
                $color-phd-accent,
                $color-black
        ),
        mba: (
                $color-mba-lead,
                $color-white,
                $color-mba-accent,
                $color-black
        ),
        emba: (
                $color-emba-lead,
                $color-white,
                $color-emba-accent,
                $color-black
        ),
        demba: (
                $color-demba-lead,
                $color-black,
                $color-demba-accent,
                $color-black
        ),
        execed: (
                $color-vivid,
                $color-white,
                $color-violet,
                $color-black
        ),
        executive-education: (
                $color-vivid,
                $color-white,
                $color-violet,
                $color-black
        ),
        online-msc: (
            $color-omba-lead,
            $color-black,
            $color-omba-accent,
            $color-black
        ),
        cce: (
            $color-cce-lead,
            $color-white,
            $color-cce-accent,
            $color-black
        )
);

.banner__content {
    @include media('>between'){
        @include grid-column-map(
            (
                'small': 12,
                'extralarge': 10,
            )
        );
    }
    
    @include grid-column-push-map(
        (
            'small': 0,
            'extralarge': 1,
        )
    );

    .banner__content__promo {
        left: 0;

        @include media('>large') {
            width: 25%;
        }

        @include media('>medium', '<=large') {
            width: 27%;
        }

        @include media('>between', '<=medium') {
            width: grid-column-width(4);
        }

        @include media('<=between') {
            width: auto;
            margin-left: #{$measure-medium / 2};
            margin-right: #{$measure-medium / 2};
        }

        @include media('>medium') {
            position: absolute;
            bottom: -#{$measure-medium * 2};
            z-index: z-index(navigation);
        }

        @include media('<=medium') {
            bottom: $measure-medium;
        }

        .banner--cutout--left & {
            right: 0;
            left: auto;

            @include media('>between', '<=medium') {
                margin-left: auto;
            }

            @include media('>medium') {
                right: 7rem;
            }

            @include media('>extralarge') {
                right: 0;
            }
        }
    }

    .banner__content__text {
        @include media('>between'){
            padding-top: $measure-enormous;
            @include grid-column(6);
        }
        @include media('<=between'){
            h1{
                width:100%;
            }
        }
    }

    .banner__content__text__item {
        margin-bottom: $measure-small;
    }

    h1,
    .banner__content__text__subtitle {
        display: inline-block;
        margin: 0;
        padding: $measure-small $measure-medium;
        width: auto;
        font-size: $font-size-beta;
        @include font-family(meta);
    }

    .banner__content__text__subtitle,
    .banner__content__text__standfirst {
        font-weight: $font-weight-light;
    }

    .banner__content__text__standfirst {
        font-size: $font-size-gamma;
    }

    @include media('<=medium') {
        .banner__content__text__subtitle,
        .banner__content__text__standfirst {
            display: none;
        }
    }

    h1 {
        font-weight: $font-weight-book;
    }

    @each $theme, $colors in $banner-themes {
        $title-background: nth($colors, 1);
        $title-color: nth($colors, 2);
        $subtitle-background: nth($colors, 3);
        $subtitle-color: nth($colors, 4);

        .banner--theme--#{$theme} & {
            h1 {
                background-color: $title-background;
                color: $title-color;
            }
            .banner__content__text__subtitle {
                background-color: $subtitle-background;
                color: $subtitle-color;
            }
        }
    }
}

@each $theme, $colors in $banner-themes {
    $title-background: nth($colors, 1);
    $title-color: nth($colors, 2);
    $subtitle-background: nth($colors, 3);
    $subtitle-color: nth($colors, 4);

    .scp--#{$theme} {
        .banner--theme--corporate {
            .banner__content {
                h1 {
                    background-color: $title-background;
                    color: $title-color;
                }
                .banner__content__text__subtitle {
                    background-color: $subtitle-background;
                    color: $subtitle-color;
                }
            }
        }
    }
}

// standfirst at bottom of banner:
.banner__standfirst {
    @extend .container;
}

.banner__standfirst__grid {
    @extend .grid;
}

.banner__standfirst__content {
    @include grid-column-map(
        (
            'small': 12,
            'medium': 10,
            'extralarge': 8,
        )
    );
    @include grid-column-push-map(
        (
            'small': 0,
            'medium': 1,
            'extralarge': 2,
        )
    );

    @include font-family(meta);
    font-size: $font-size-delta;
    color: $color-emperor;
    font-weight: $font-weight-light;
    text-align: center;
}

.banner__freehtml {
    position: relative;

    > div {
        z-index: z-index(above);
        @extend .container;

        > div {
            @extend .grid;

            > div {
                @include grid-column-map(
                    (
                        'small': 12,
                        'extralarge': 10,
                    )
                );
                @include grid-column-push-map(
                    (
                        'small': 0,
                        'extralarge': 1,
                    )
                );
            }
        }
    }

    &:after {
        position: absolute;
        display: block;
        content: '';
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: z-index(default);

        background: rgba(0, 0, 0, 0.8);

        @include media('>medium') {
            background: linear-gradient(
                rgba(0, 0, 0, 0),
                rgba(0, 0, 0, 0.8) $measure-large
            );
        }
    }
}
