////
/// Text CTAs
/// @group components
////

/**
USED IN:
 - 361894 (DEV)

 */
.banner--text-ctas {
    padding-top: $measure-enormous;
    @include grid;

    .banner__text {
        @include grid-column(6);

        position: relative;

        .banner__text__subtitle {
            @include banner-text-subtitle;
        }

        p {
            @include standfirst;
        }
    }

    .banner__ctas {
        padding-right: $grid-padding-half;

        @include media('>small') {
            margin-left: auto;
        }
        @include media('<=medium') {
            margin-right: auto;
        }

        @include media('<=small') {
            width: 100%;
            padding-left: $grid-padding-half;
        }

        .cta-group--margin-left {
            margin-left: 0;
        }

        .cta-group--stretched {
            margin-bottom: $measure-small;

            @include media('<=small') {
                a:first-child {
                    margin-bottom: $measure-medium;
                }
            }

            @include media('>small', '<=between') {
                a:first-child {
                    margin-bottom: $measure-small;
                }
            }
        }
        .banner__ctas--margin-bottom {
            .cta {
                @include media('>small', '<=between') {
                    bottom: $measure-medium;
                }
                @include media('>between') {
                    bottom: $measure-small;
                }
            }
        }
    }
}
