.bayes-news{
    padding-top: 3rem;
    padding-bottom: 3rem;
    padding-left: 3rem;
    padding-right: 3rem;
    @include grid-top;
}

.bayes-news__wrapper{
    @include media('>medium'){
        display: flex;
        align-items: center;
    }

    @include media('<=medium'){
        width: fit-content;
    }
}

.bayes-news__title{
    position: relative;
    padding: 0.5rem 2rem;
    margin-right: $measure-medium;

    @include media('<=medium'){
        margin-bottom: $measure-medium;
    }

    h2{
        line-height: 1;
        margin-bottom: 0;
        font-size: 2.75rem;
    }
}

.bayes-news__list{
    @include media('>between'){
        display: flex;
    }

    @include media('>between', '<medium'){
        flex-wrap: wrap;
    }
    
    margin-top: 2rem;

    .feature-news__card--main{
        @include media('>medium'){
            width: 30%;
            margin-right: 3%;
        }

        @include media('>between', '<=medium'){
            width: 48%;

            &:nth-child(odd){
                margin-right: 2%;
            }
        }

        .news__image-credit{
            padding: .2rem .4rem;
            bottom: unset;
            top: 0;
            right: 0;
            z-index: 1;
        }
    }

    .feature-news__card__body__title{
        font-weight: bold;
    }

    .news__image-credit {
        .image-credit__button{
            background-color: $transparent;
            color: $color-white;
            width: 2.25em;
            height: 2.25em;
            float: right;
            padding: 0;
            border: 0;
            position: relative;
            cursor: pointer;
            text-shadow: 0 0 0.5em rgba($color-black, 0.75);
    
            &:focus,
            &:active {
                text-shadow: 0 0 0.5em $color-white;
                outline-color: $color-white;
            }
        }
    }
}