.person-profiles-container {
    @include media('>small') {
        display: grid;
        grid-template-columns: repeat(2, auto);
        grid-column-gap: 1.5rem;
    }

    @include media('>large') {
      grid-template-columns: repeat(4, auto);
    }
}

.person-profile {
    @include person-profile;
    margin: 0 0 1.5rem 0;
}

.person-profile__img {
    img {
        max-width: 8.125rem;
    }
}

.person-profile__details {
    padding-left: .625rem;
}

.person-profile__title,
.person-page__jobtitle {
    margin-bottom: 1rem;
}

.person-page {
  @include media('>between') {
    display: grid;
    grid-template-columns: auto auto;
    grid-column-gap: 2rem;
  }
}

.person-page__social {
  margin-top: 2rem;

  .fa {
    margin-right: .5rem;
  }

  a {
    
  }

  li {
    margin-bottom: 1rem;
  }
}

.person-page__description {
  @include media('<=between') {
    margin-top: 1rem;
  }
}

.recent-articles{

  h2{
    margin-top: $measure-large;
  }

  .feature-news__card{
    width: 25%;
    display: inline-block;
    vertical-align: top;

    @include media('<=medium'){
      width: 50%;
    }

    @include media('<=small'){
      width: 100%;
    }
  }
  .feature-news__card:nth-child(5), .feature-news__card:nth-child(6){
    display: inline-block;
  }
  .item__date{
    display: block;
    margin-bottom: 0.5rem;
  }
  .news-card-topic{
    display: inline-block;
  }
}