////
/// Nav Mobile
/// @group components
////

.nav-mobile {
    @include grid-column-base;
    font-size: $font-size-small;

    @supports (display: flex) {
        align-self: center;
    }

    @include media('>medium') {
        display: none;
    }

    @include media('<=medium') {
        width: grid-column-width(2);
    }

    @include media('<=between') {
        width: grid-column-width(4);
    }
}

.nav-mobile__close-link {
    @include anchor(reset);
    @include anchor(nav-mobile-link);
}

.nav-mobile__close-link {
    @include absolute(top-right);
    height: 100%;
}

.nav-mobile__close-link {
    span {
        @extend .fa-close;
    }
}

.nav-mobile__toggle {
    display: none;
}

.nav-mobile__content {
    background-color: $color-white;
    color: $color-black;
    width: 50%;
    min-width: $fixed-sidebar-width;
    height: 100%;
    box-shadow: box-shadow(alpha);
    z-index: z-index(navigation);

    .nav-mobile__toggle:not(:checked) + .nav-mobile__overlay & {
        @include visually-hidden;
    }

    .nav-mobile__toggle:checked + .nav-mobile__overlay & {
        @include animation(false, nav-mobile-in);
    }
}

@keyframes nav-mobile-in {
    0% {
        opacity: 0;
        visibility: hidden;
    }
    0.001% {
        opacity: 1;
        visibility: visible;
        transform: translateX(100%);
    }
    100% {
        opacity: 1;
        visibility: visible;
        transform: translateX(0);
    }
}

.nav-mobile__toggle-label {
    @include non-selectable;
    background-color: $color-white;
    align-self: flex-end;
    display: inline-block;
    padding-top: $measure-small;
    padding-bottom: $measure-small;
    border-width: 0;
    vertical-align: top;
    font-weight: $font-weight-bold;
    text-align: center;
    text-transform: uppercase;
    white-space: nowrap;

    &:before {
        @include font-family(icon);
        content: '#{$fa-var-bars} ';
        vertical-align: middle;
        font-size: $font-size-gamma;
        font-weight: $font-weight-default;
    }

    &.open {
        &:before {
            content: '#{$fa-var-close} ';
        }
    }

    &:not([class*='open']) {
        @include anchor(outline);
        background-color: $color-white;
    }

    .nav-mobile__toggle:checked + .nav-mobile__overlay & {
        padding: $measure-medium $measure-small;
        position: fixed;
        top: 0;
        right: $measure-medium;
        z-index: z-index(modal);
    }
}

.nav-mobile__close-label {
    background-color: rgba($color-black, opacity(half));
    width: 100%;
    height: 100%;
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    z-index: z-index(above);

    .nav-mobile__toggle:checked + .nav-mobile__overlay & {
        display: block;
    }
}

.nav-mobile__tertiary {
    background-color: $color-white;
    padding-top: $measure-small;
}

/** open state **/

.nav-mobile__overlay {
    .nav-mobile__toggle:checked + & {
        width: 100%;
        height: 100%;
        overflow-x: hidden;
        overflow-y: auto;
        position: fixed;
        top: 0;
        right: 0;
        z-index: z-index(navigation);
    }
}

.nav-mobile__container {
    @supports (display: flex) {
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: flex-end;
    }

    .nav-mobile__toggle:checked + .nav-mobile__overlay & {
        width: 100%;
        height: 100%;
        overflow-x: hidden;
        overflow-y: auto;
        position: absolute;
        top: 0;
        right: 0;
        z-index: z-index(navigation);
    }
}

.explore-theme {
    .nav-mobile__toggle-label {
        @include anchor(nav-mobile-link--dark-bg);
        color: $color-opaque;
        background-color: $color-black;

        &.open {
            background-color: $color-white;
            @include anchor(nav-mobile-link);
        }
    }

    .site-search__container__toggle-label {
        @include anchor(nav-mobile-link--dark-bg);
    }
}