.unibuddy-profiles {
    padding: 2rem 0;

    .container {
        display: flex;
    }
}

.unibuddy-card {
    box-shadow: 0px 0px 30px 10px #e0e0e0;
    padding: 2rem 1.5rem;

    @include media('>between') {
        max-width: 19rem;
    }

    p {
        margin-top: 1rem;
    }
}

.unibuddy-card--mba {
    max-width: 100%;
    margin-bottom: $measure-large;
}

.unibuddy-card__cta {
    padding: 1rem 2rem;
    color: $color-black;
    border-bottom: none;
    font-weight: $font-weight-medium;
    display: inline-block;

    span {
        margin-left: 1rem;
    }

    &:hover,
    &:active {
        color: $color-black;
    }
}

.unibuddy-card__info {
    display: flex;
    align-items: center;
}

.unibuddy-card__details__flag {
    max-width: 3rem;
    margin-top: 0.5rem;
}

.unibuddy-card__image {
    margin-right: 1.5rem;
    max-width: 5rem;

    img {
        border-radius: 100%;
    }
}

.unibuddy-card__details__name {
    font-weight: $font-weight-bold;
    margin-bottom: 0;
}

.unibuddy-card__details__title {
    font-weight: $font-weight-bold;
}

.unibuddy-card--ug {
    .unibuddy-card__details__name {
        color: $color-flamingo;
    }

    h4 {
        &:after {
            background: $color-yellow;
        }
    }

    .unibuddy-card__cta {
        background: $color-yellow;

        &:hover,
        &:active,
        &:focus {
            background-color: #faa21d;
        }
    }
}

.unibuddy-card--msc {
    .unibuddy-card__details__name {
        color: $color-allports;
    }

    h4 {
        &:after {
            background: $color-allports;
        }
    }

    .unibuddy-card__cta {
        color: $color-white;
        background: $color-allports;

        &:hover,
        &:active,
        &:focus {
            background-color: #006081;
        }
    }
}

.unibuddy-card--mba {
    .unibuddy-card__details__name {
        color: $color-sherpa;
    }

    h4 {
        &:after {
            background: $color-sherpa;
        }
    }

    .unibuddy-card__cta {
        @include anchor-cta--sherpa();
    }
}

.unibuddy-card--noresults {
    text-align: center;

    h4 {
        &:after {
            content: '';
            display: block;
            width: 80px;
            height: 3px;
            margin: 20px auto;
            max-height: 240px;
        }
    }
}
