.video-banner--popup {
    height: 320px;
    overflow: hidden;
    position: relative;

    img.video-popup__img {
        margin: 0;
        position: relative !important;
        top: 0;
        object-fit: cover;
        width: 100%;
        height: 320px;
    }

    .video-preview {
        padding-top: 0;
        margin-bottom: 0;
    }
}

.video-banner--popup.video--playing {
    transition: margin 2s ease;
    transition: padding 2s ease;
    transition: height 2s ease;
    margin: 0 auto;
    padding: $measure-xlarge;
    background-color: $color-black;
    height: fit-content;
}

.video-container {
    position: relative;
    padding-bottom: 56.25%; /*16:9*/
    height: 0;
    overflow: hidden;
}

.video-container iframe,
.video-container object,
.video-container embed {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 2;
}

.dialog__close--video-popup {
    position: absolute;
    right: 10px;
    top: 0;
    font-size: 1.6rem;
    color: $color-white;

    @include media('>medium') {
        right: 50px;
    }
}

@keyframes spin {
    to {
        transform: rotate(360deg);
    }
}

.video-preview__hover {
    &:hover:before {
        opacity: 0.9;
        background: none;
        transition: 0.4s ease-out;
    }
}

.video-preview--overlay:before {
    content: '';
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 2;
    background: rgba(156, 156, 154, 0.5);
    transition: 0.4s ease-out;
}

.video-preview--overlay-orange:before {
    content: '';
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 2;
    background: rgba(251, 174, 59, 0.5);
    transition: 0.4s ease-out;
}

.video-popup__button {
    position: absolute;
    background-color: transparent;
    z-index: 2;
    border-bottom: none;

    svg {
        width: 100px;
        height: 100px;
        top: 35%;
        fill: none;
        margin: 0 auto;

        @include media('<small') {
            width: 80px;
            height: 80px;
        }

        .stroke-dotted {
            opacity: 0;
            stroke-dasharray: 1, 8;
            stroke-width: 4px;
            stroke-linecap: round;
            transform-origin: 50% 50%;
            animation: spin 20s infinite linear;
            transition: opacity 1s ease, stroke-width 1s ease;
            stroke: $color-white;
        }

        .stroke-solid {
            stroke-dashoffset: 0;
            stroke-dasharray: 300;
            stroke-width: 4px;
            transition: stroke-dashoffset 1s ease, opacity 1s ease;
            stroke: $color-white;
        }

        .icon {
            transform-origin: 50% 50%;
            transition: transform 200ms ease-out;
            fill: $color-white;
        }

        .circle {
            fill: $color-white;
            fill-opacity: 0.2;
        }

        &:hover {
            .stroke-dotted {
                stroke-width: 4px;
                opacity: 1;
            }

            .stroke-solid {
                opacity: 0;
                stroke-dashoffset: 200;
            }

            .icon {
                transform: scale(1.3);
            }

            .circle {
                fill: $color-white;
                fill-opacity: 0;
            }
        }
    }
}

.video-popup__button:focus,
.video-popup__button:active {
    svg {
        outline: 3px $color-white solid;
    }
}

.video-preview--caption {
    display: none;
}

.video-banner--popup {
    figcaption {
        display: none;
    }

    &.video--playing figcaption {
        display: block;
        color: $color-white;
        margin: $measure-medium 0 0;
        padding: 0 10px;

        @include media('>small', '<=medium') {
            margin: $measure-medium 16.667% 0;
        }

        @include media('>medium') {
            margin: $measure-medium 25% 0;
        }
    }

    .figcaption__text {
        display: block;
        margin-top: $measure-small;
    }
}

.video-preview__title {
    color: $color-white;
    display: block;
    font-size: $font-size-delta;
    font-weight: $font-weight-bold;
    text-align: center;
    margin: $measure-xlarge auto $measure-large;

    @include grid-column(6);
}
