.news-events-landing {
    .featured-news,
    .row-fluid {
        width: 100%;
    }

    h2 {
        padding-left: $measure-small;
        padding-right: $measure-small;
    }

    .news-articles-list {
        width: 100%;
    }

    .featured-events {
        width: 100%;
    }
}
.featured-events .grid--thirds {
    width: 100%;

    li {
        list-style: none;

        @include media('<=medium') {
            width: 80vw;
        }
    }
}
.featured-event {
    padding-left: $measure-small;
    padding-right: $measure-small;
}
.featured-item__image__wrap {
    position: relative;
    padding-bottom: 60%;
    overflow-y: hidden;
}

.featured-item__image {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;

    img {
        width: 100%;
    }
}

.featured-item__content {
    box-shadow: 0 0 1.5rem 0.187rem rgba(0, 0, 0, 0.15);
    background-color: $color-white;
    position: relative;
    top: -2.5rem;
    width: 90%;

    display: flex;
    flex-direction: row;
}

.featured-item__content__date__wrap {
    background: $color-alizarin;
    width: 20%;
    text-align: center;
    color: $color-white;
    display: flex;
    flex-direction: column;

    time {
        text-transform: uppercase;
        margin-top: auto;
        margin-bottom: auto;
    }
}

.featured-item__content__date__day {
    font-weight: $font-weight-light;
    line-height: 1;
}

.featured-item__content__date__month {
    font-weight: $font-weight-medium;
}

.featured-item__content__details__wrap {
    padding: $measure-medium;
    width: 80%;
}

.featured-item__content__details__time {
    margin-bottom: $measure-small;
    color: $color-lucifer;
    font-size: 0.875rem;
    font-weight: $font-weight-medium;
    
    &:before {
        content: '\f017';
        font-family: FontAwesome;
        margin-right: $measure-small;
        margin-bottom: $measure-small;
    }
}

.featured-item__content__details__title {
    width: 100%;
    padding: 0;
    margin-bottom: $measure-small;
    font-size: $font-size-delta;

    a{
        @include multi-line-animation;
        font-family: $font-family-meta;
        font-weight: $font-weight-medium;

    }
}

.featured-item__content__details__topic {
    .news-article__anchor {
        color: $management;
        display: inline-block;
        height: 100%;
        @include anchor-border-float-in($management);
    }
}

.featured-events__container__cta__wrap {
    display: inline-block;
    margin-bottom: $measure-large;
    width: 100%;
    @supports (display: flex) {
        display: flex;
        flex-direction: row-reverse;
    }
}
.featured-events__container__cta {
    float: right;

    @include anchor(skate);
    @include media('<=small') {
        margin-right: $measure-small;
    }
}

.promo {
    width: 100%;
    color: $color-white;
    position: relative;
    margin-top: $measure-large;

    &:before {
        content: '';
        width: 100%;
        position: absolute;
        height: 60%;
        background: $color-prussian;
    }
}

.promo__container__wrap {
    margin-top: $measure-xlarge;
    margin-bottom: $measure-xlarge;

    @include media('<=medium') {
        padding-left: $measure-large;
        padding-right: $measure-large;
    }

    @include media('<=small') {
        padding-left: $measure-medium;
        padding-right: $measure-medium;
    }
}

.promo__container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    background: $color-white;
}

.promo__container__image {
    grid-column: 1 / 2;
    overflow: hidden;
    text-align: center;

    @include media('<=medium') {
        grid-column: 1 / -1;
    }
}

.promo__container__image img {
    height: 100%;
    width: auto;
    max-width: inherit;

    @include media('<=medium') {
        width: 100%;
    }
}

.promo__container__text {
    grid-column: 2 / 3;
    background: $color-white;
    color: $color-black;
    padding: $measure-large;

    @include media('<=medium') {
        grid-column: 1 / -1;
    }
}

.promo_container__text_subtitle {
    font-size: $font-size-gamma;
    margin-bottom: $measure-medium;
    font-weight: $font-weight-medium;
    @include font-family(meta);
}

.promo__twitter-tag {
    color: $color-black;
    margin-top: $measure-large;

    @include media('<=medium') {
        padding-left: $measure-large;
        margin-top: 0;
    }

    .fa-twitter {
        font-family: 'fontAwesome';
    }

    span {
        color: $color-twitter-blue;
    }
}



.cass-news-events-search__footer{
    background: $color-white-smoke;
    .grid{
        justify-content: center;
    }
}
