.vertical-menu__page-link,
.vertical-menu__toggle-link {
    @include anchor(reset);
    @include anchor(nav-mobile-link);
}

.vertical-menu__page-link {
    margin-right: #{$measure-medium * 3};
    left: $border-width-hairline;
    &:focus {
        outline: $border-width-default solid $color-black;
    }
    &:active {
        outline: $border-width-default dotted $color-black;
    }
}

.vertical-menu--l1 > .vertical-menu__header > .vertical-menu__page-link {
    padding: $measure-medium;
}

.vertical-menu__page-link.vertical-menu__page-link--t2 {
    pointer-events: none;

    span {
        border-bottom: $border-width-default $border-style-default
            $color-alizarin;
    }
}

.vertical-menu__toggle-link {
    @include absolute(top-right);
    font-size: $font-size-small;
    height: 100%;
    right: $border-width-default;
    &:focus {
        outline: $border-width-default solid $color-black;
    }
    &:active {
        outline: $border-width-default dotted $color-black;
    }

    span {
        @extend .fa-plus;
    }

    .vertical-menu.open > .vertical-menu__header & span {
        @extend .fa-minus;
    }
}

.vertical-menu--l0 > .vertical-menu__header {
    padding-top: $measure-medium;
    padding-bottom: $measure-medium;
    border-bottom: $border-width-thick $border-style-default #e5e5e5;
}

$level-colors: #ffffff, #ffffff, #ffffff, #f2f2f2, #e5e5e5, #d8d8d8, #cbcbcb,
    #bebebe, #a1a1a1;
@for $i from 1 through length($level-colors) {
    .vertical-menu--l#{$i - 1} {
        $color: nth($level-colors, $i);
        background-color: $color;
        color: contrasting-color($color);

        @if ($i > 2) {
            & > .vertical-menu__header > .vertical-menu__page-link {
                padding-left: $measure-large;
            }
        }
    }
}

.vertical-menu--l0,
.vertical-menu--l1 {
    & > .vertical-menu__header {
        font-weight: $font-weight-medium;
    }
    .vertical-menu__toggle-link {
        top: $border-width-default;
    }
}

.vertical-menu--l1.open {
    & > .vertical-menu__header,
    & > ul {
        border-bottom: $border-width-hairline $border-style-default #a1a1a1;
    }
}

.vertical-menu__header {
    position: relative;
}

.vertical-menu {
    ul:not([class]) {
        margin: 0 !important;
    }

    li {
        border-bottom: $border-width-hairline $border-style-default
            $color-gallery;
        position: relative;
    }

    &.vertical-menu--left {
        right: 100%;
        left: -100%;
    }
    &.vertical-menu--center {
        right: 0;
        left: 0;
    }
    &.vertical-menu--right {
        right: -100%;
        left: 100%;
    }

    &:not([class*='open']) > ul {
        display: none;
    }
}
