.link__external {
    &:after {
        font-family: $font-family-icon;
        content: '\f08e';
        padding-left: 0.3rem;
        font-size: 12px;
    }
}

.link__pdf {
    &::before {
        font-family: $font-family-icon;
        font-size: $font-size-default;
        content: '\f1c1';
        padding-right: 0.5rem;
    }
}
