.swiper-viewport {

    overflow: hidden;
    width: 100%;
}

.swiper-container {
    grid-column: 1/-1;
    position: relative;
    width: 100%;
}

.js {
    .swiper-wrapper {
        --n: 1;
        --tx: 0px;
        display: flex;
        align-items: center;
        overflow-y: hidden;
        width: 100%;
        width: calc(var(--n) * 100%);
        // height: 50vw;
        // max-height: 100vh;
        transform: translate(
            calc(var(--tx, 0px) + var(--i, 0) / var(--n) * -100%)
        );
        align-items: baseline;
        list-style: none;
        max-width: none;
        margin-top: 0;
        margin-left: 0;

        @include media('<=between') {
            height: inherit;
            max-height: 25rem;
        }
    }
}

.swiper-slide {
    width: 100%;
    width: calc(100% / var(--n));
    user-select: none;
    pointer-events: none;
    position: relative;
}

.js {
    .swiper-slide {
        opacity: 0.7;

        &.active {
            opacity: 1;
            transition: opacity 0.6s;

            .swiper-slide__text {
                opacity: 1;
            }
        }
    }

    .swiper-slide__text {
        position: absolute;
        top: 12%;
        padding: 1rem;
        color: $color-white;
        margin-right: auto;
        margin-left: auto;
        opacity: 0;
        transition: opacity 2s;
        font-weight: $font-weight-bold;
        line-height: 0.75rem;
        max-width: 60ch;

        h2{
            font-size: $font-size-kappa;
            text-shadow: 0 2px 4px rgba(0, 0, 0, 0.3);
            line-height: 1.2;
            @include media('<=between'){
                font-size: $font-size-beta;
                line-height: 1.14;
            }

            @include media('<min'){
                font-size: $font-size-delta;
            }
        }

        p {
            max-width: none;
            font-weight: $font-weight-bold;
            line-height: 2rem;
            font-size: 1.25rem;
            text-shadow: 0 2px 4px rgba(0, 0, 0, 0.3);

            @include media('<min'){
                font-size: $font-size-default;
            }
        }

        a {
            pointer-events: auto;

            &:active,
            &:focus{
                outline-color: $color-white;
                outline-offset: $border-width-hairline;
            }
        }

        @include media('<extralarge') {
            margin-right: none;
            margin-left: 1rem;
            padding: 1rem;
        }

        @include media('>=extralarge'){
            left: calc((100% - 1440px)/2);
        }

        @include media('>=min'){
            top: 50%;
            transform: translate(0, -50%);
        }

        @include media('<small'){
            max-width: calc(100% - 2rem);
            padding: unset;

            .cta {
                padding: 1rem;
            }
        }
    }
}

.swiper-slide__image-wrapper {
    overflow: hidden;
    background-color: $color-lightgrey;
    @include media('<=between') {
        overflow: hidden;
        max-height: 450px;
    }

    img {
        object-fit: cover;
        width: 100%;
        @include media('<large') {
            max-height: 30rem;
        }
        @include media('>=large') {
            max-height: 40rem;
        }
    }

    &:after{
        content:'';
        width: 100%;
        height: 100%;
        background-image: linear-gradient(to right, rgba(0, 0, 0, 0.8) 0%, rgba(0, 40, 77, 0) 100%);
        position: absolute;
        top: 0;

        @include media('<medium'){
            background-image: initial;
            background-color: rgba(0, 0, 0, 0.8);
        }
    }
}

.js {
    .lazy {
        display: none;
    }
}

.no-js {
    .lazy-img {
        display: none;
    }
}

.swiper-indicator {
    position: absolute;
    font-size: 1rem;
    font-weight: bold;
    color: $color-lightgray;
    bottom: 1rem;
    right: 7rem;

    @include media('>between') {
        bottom: 3rem;
        right: 1rem;
    }
}

.swiper-indicator__active-slider {
    color: red;
}

.swiper-indicator__slider-length {
    color: grey;
}

.swiper-buttons-wrap {
    padding: 1rem;
    bottom: 0;
    display: flex;
    position: absolute;

    @include media('>=extralarge'){
        left: calc((100% - 1440px)/2);
    }

    @include media('<medium'){
        bottom: 0;
        right: 0;
    }

    @include media('<min'){
        padding: 0.5rem;
    }
}

.swiper-slider-arrow {
    position: relative;

    @include media('<small'){
        height: 50px;
        width: 50px;
    }

    & > *{
        pointer-events: none;
    }

    &:disabled{
        .prev-svg,
        .next-svg{
            opacity: 0.3;
        }
    }

    &:hover:enabled{
        .stroke-dotted {
            stroke-width: 4px;
            opacity: 1;
        }

        .stroke-solid {
            opacity: 1;
            stroke-dashoffset: 0;
        }

        .icon {
            transform: scale(1.3);
        }

        .circle {
            fill: $color-white;
            fill-opacity: 0;
        }
    }

    &:active,
    &:focus{
        outline: initial;
        &:after{
            content: '';
            position: absolute;
            top: 5%;
            left: 5%;
            width: 90%;
            height: 89%;
            border: solid 0.125rem $color-white;
            border-radius: 50%;
        }
    }

    &:active{
        &:after{
            border-style: dotted;
        }
    }

    .stroke-solid{
        opacity: 0;
        stroke-dasharray: 300;
        stroke-width: 4px;
        transition: stroke-dashoffset 1s ease, opacity 1s ease;
        stroke: #ffffff;
        fill: none;
    }

    .stroke-dotted{
        opacity: 0;
        stroke-dasharray: 1, 8;
        stroke-width: 4px;
        stroke-linecap: round;
        transform-origin: 50% 50%;
        -webkit-animation: spin 20s infinite linear;
        animation: spin 20s infinite linear;
        transition: opacity 1s ease, stroke-width 1s ease;
        stroke: #ffffff;
        fill: none;
    }

    .circle{
        stroke-dashoffset: 0;
        stroke-dasharray: 300;
        stroke-width: 4px;
        transition: stroke-dashoffset 1s ease, opacity 1s ease;
        stroke: rgba(255, 255, 255, 0.5);
        fill: none;
    }

    .prev-svg,
    .next-svg{
        transform: translate(30px, 30px) scale(.025, .025);
        transition: transform 200ms ease-out;
        fill: #ffffff;
    }

    .next-svg{
        transform: translate(70px, 70px) scale(.025, .025) rotate(180deg);
        transition: transform 200ms ease-out;
        fill: #ffffff;
    }
}



.swiper-button-next{
    svg{
        .stroke-solid{
            stroke-dashoffset: -360;
        }
    }
}

.swiper-button-prev {
    @include media('>small'){
        margin-right: 1rem;
    }
    

    svg{
        .stroke-solid{
            stroke-dashoffset: -360;
        }
    }
}

.smooth {
    transition: transform 0.5s ease-out;
}

