.cass-events {
    position: relative;
    margin-bottom: $measure-large;

    .banner--cutout--none {
        margin-bottom: 0;
    }

    .cass-events__main__left {
        width: 33.333%;
        padding: $measure-xlarge $measure-large;

        @include media('<large') {
            padding: $measure-medium $measure-small;
        }

        @include media('<medium') {
            width: 100%;
        }
    }

    .cass-events__main__right {
        width: 66.667%;
        position: relative;
        padding: $measure-xlarge $measure-large;

        &:before {
            position: absolute;
            top: -$measure-large;
            background: $color-white-smoke;
            -webkit-user-select: none;
            -moz-user-select: none;
            -ms-user-select: none;
            user-select: none;
            -webkit-touch-callout: default;
            -webkit-touch-callout: none;
            content: '';
            width: 100%;
            height: calc(100% + 2rem);
            pointer-events: none;
            left: 0;
        }

        @include media('<large') {
            padding: $measure-medium $measure-small;
        }

        @include media('<medium') {
            width: 100%;
        }
    }

    .events-listing__wrap{
        position: relative;
    }

    .events-listing__header {
        display: inline-block;
        margin-bottom: $measure-medium;
    }

    .events-listing__heading__before {
        display: inline;
    }
    .events-listing__heading {
        @include events-news-listings-heading();
    }

    .events-listing__heading h2 {
        padding: 0 1rem;
        display: inline;
    }

    #managed-form-container {
        width: 100%;
    }

    .cass-events__filter-results {
        margin-top: 0;
        padding-top: 0;
    }
    .course-finder__main {
        background: none;
    }

    .featured-container {
    }

    .cass-events__query {
        margin-bottom: $measure-large;

        @include media('<=between') {
            margin-bottom: $measure-medium;
        }
    }

    .cass-events__query__header {
        font-size: $font-size-gamma;
    }

    .cass-events__query .query-box {
        display: inline-block;
        width: 100%;
        position: relative;
        margin-bottom: $measure-xlarge;

        @include media('<=between') {
            margin-bottom: $measure-medium;
        }
    }
    .cass-events__query #query-box__query {
        @include styled-input-box;
    }

    .cass-events__query button {
        background: $color-alizarin;
        color: $color-white;
        height: 100%;
        position: absolute;
        right: 0;
        padding: $measure-small $measure-medium;
    }

    .cass-events__query__filter {
        h2 {
            @include media('<=small') {
                display: none;
            }
        }
    }

    .icon-search:before {
        font-family: 'FontAwesome';
        content: '\f002';
    }

    .date-selector__wrap {
        border-bottom: $border-width-hairline $border-style-default $color-nobel;
        padding-bottom: $measure-medium;

        .more-events-link__wrap {
            display: inline;
            @include media('<between') {
                display: block;
                margin-top: $measure-medium;
            }
        }

        .more-events-link {
            @include anchor(skate);
            vertical-align: middle;
        }
    }

    /* calendar styles */
    .date {
        float: left;
        display: inline;
        font-size: small;
        text-align: center;
        background: #fff;
        margin: 0 0.625rem 0 0;
        width: 70px;

        abbr {
            border-bottom: none;
            cursor: pointer;
        }

        .date-month {
            background: $color-alizarin;
            color: $color-white;
            margin: 0;
            padding: 0;
            font-weight: bold;

            text-transform: uppercase;

            .abbr {
                border-bottom: none;
                cursor: pointer;
            }
        }
        .date-day-no {
            font: 2.2em/1.1em Georgia, serif;
            color: grey;
            padding: 0;
            margin: 0;
            border: $border-width-hairline solid #ccc;
            border-width: 0 $border-width-hairline 0px $border-width-hairline;
        }
        .date-day {
            font: 0.9em Georgia, serif;
            padding: 0 0 5px 0;
            margin: 0;
            border: $border-width-hairline solid #ccc;
            border-width: 0 $border-width-hairline $border-width-hairline
                $border-width-hairline;
        }
        .duration {
            margin: $border-width-hairline 0 0 0;
            background: #e0dbd7;
            width: 100%;
            display: block;
            padding: $border-width-hairline 0;
        }
    }

    // list of events
    .vevent {
        list-style: none;
    }
    .events-list {
        list-style: none;
        margin: 0;
        padding: 0;

        li {
            list-style: none;
            margin: 0;
            overflow: hidden;
            border-top: $border-width-hairline solid #ccc;
            padding: 1.25rem 0;

            &:first-child {
                border-top: none;
            }
        }
        .date {
            float: left;
            margin-bottom: 1.25rem;
        }
        .event-details {
            h2 {
                margin-top: 0;
                font-size: 1.4em;
                line-height: 1.4em;
            }
            .time,
            .category,
            .audience {
                display: inline;
                margin-right: 0.625rem;
                margin-bottom: 0;
            }
            .time {
                clear: left;
            }
            .event-summary {
                display: block;
                clear: left;
                margin: 0;
            }
        }
    }

    .event {
        .event-time,
        .event-category,
        .event-audience,
        .event-audience-note {
            display: inline;
            margin: 0 0.625rem 0 0;
            color: #666;
        }
    }

    // event forms
    #events-form,
    #opendays-form {
        fieldset {
            padding: 0;
            margin: 0;
            border: none;
        }
    }

    /*
    * ===============================================================> from calendar
    * ===============================================================> from calendar
    */
    ////
    /// Calendar
    /// @group components
    ////

    .calendar-wrap {
        @include media('<medium') {
            display: none;
        }
    }

    .calendar {
        width: 100%;
        display: none;
        display: block;
    }

    .calendar__title {
        width: 100%;
        margin-bottom: $measure-small;
        white-space: nowrap;
        font-size: 16px;
        font-weight: 600;
        line-height: 2.1875;
        text-align: center;
        display: flex;
        flex-direction: row;
    }

    .calendar__title__button {
        padding: 0 $measure-small;
        border-radius: $border-width-default;
        font-size: 14px;
        font-weight: 600 !important;
        line-height: 2.143;
        text-align: center;
        vertical-align: top;
        cursor: pointer;
        width: 10%;
        border: $border-width-hairline $border-style-default $color-nobel;

        &:before,
        &:after {
            font-family: 'FontAwesome';
        }

        &.calendar__title__button--previous {
            margin-right: $measure-small;
            float: left;

            @supports (display: flex) {
                margin-right: 0;
            }

            &:before {
                content: '\f060';
            }
        }

        &.calendar__title__button--next {
            margin-left: $measure-small;
            float: right;

            @supports (display: flex) {
                margin-left: 0;
            }

            &:after {
                content: '\f061';
            }
        }
    }

    .calendar__title__month {
        width: 80%;
        border: $border-width-hairline $border-style-default $color-nobel;
    }

    .calendar__range {
        padding-left: $measure-medium;
        border-bottom: $border-width-hairline $border-style-default $color-black;
        margin-bottom: 1.5em;
        position: relative;
        z-index: 2;

        label {
            display: inline-block;
            margin-bottom: -$border-width-hairline;
        }
    }

    .calendar__radio__button {
        background-color: $color-emperor;
        color: $color-black;
        display: inline-block;
        border: $border-width-hairline $border-style-default $color-black;
        padding: 0 $measure-large;
        position: relative;
        border-radius: $border-width-default $border-width-default 0 0;
        font-size: 14px;
        font-weight: 600 !important;
        line-height: 2.5;
        text-align: center;
        cursor: pointer;
        z-index: 0;

        input:checked + & {
            background-color: transparent;
            color: $color-black;
            border: $border-width-hairline $border-style-default $color-black;
            border-bottom-color: $color-gallery;
            z-index: 3;
        }

        &:before {
            display: inline-block;
            font-family: 'FontAwesome';
        }

        input + & {
            &:before {
                content: '\f10c';
                color: msday;
                font-size: 1.25em;
                margin-right: $measure-small;
            }
        }

        input:checked + &:before {
            content: '\f05d';
            color: $color-alizarin;
        }
    }

    .calendar__head,
    .calendar__days {
        width: 100%;
        padding: 0 !important;
        margin: 0 !important;
        list-style: none !important;

        @supports (display: flex) {
            display: flex;
            flex-direction: row;
            flex-wrap: nowrap;
        }

        li {
            box-sizing: border-box;
            width: 14.3%;
            float: left;
            margin: 1px;

            @include media('<large') {
                width: 13.6%;
            }

            @supports (display: flex) {
                float: none;
                display: flex;
                flex: 0 0 auto;
                flex-direction: column;
            }
        }
    }

    .calendar__head {
        li {
            color: grey;
            padding: $measure-medium $measure-small;
            border-width: 0;
            font-weight: 600;
            text-align: center;
        }
    }

    .calendar__days {
        li {
            border: none;
            position: relative;
            padding-top: 14.26%;
            overflow: hidden;

            @include media('<large') {
                padding-top: 13.6%;
            }

            a {
                box-sizing: border-box;
                color: inherit;
                width: 100%;
                border: $border-width-hairline $border-style-default transparent;
                border-radius: 50%;
                display: block;
                flex: 1 0 auto;
                text-align: center;
                position: absolute;
                top: 0;
                left: 0;
                right: 0;
                bottom: 0;

                &:hover,
                &:focus,
                &:active {
                    background-color: transparent;
                    cursor: pointer;
                }

                &:hover {
                    border: $border-width-hairline $border-style-default
                        $color-alizarin;
                    transition: none;
                }

                &:focus {
                    outline: none;
                }
            }

            &.is-today a {
                border: $border-width-hairline $border-style-default
                    $color-alizarin;
                border-radius: 50%;

                .calendar__date {
                    color: $color-alizarin;
                }

                .calendar__event:before {
                    color: $color-alizarin;
                }
            }

            // `.is-selected` must override `.is-today`
            &.is-selected a {
                background: $color-alizarin;
                border-radius: 50%;
                .calendar__date {
                    color: $color-white;
                }
                .calendar__event:before {
                    color: $color-white;
                }
            }
        }

        &:last-child {
            li {
            }
        }
    }

    .calendar__days__day--other-month {
        display: none;

        a {
            .calendar__date {
                color: grey;
            }
        }
    }

    // Label which shows today's date
    .calendar__date {
        width: 100%;
        height: $measure-large;
        display: block;
        padding: $measure-small;
        top: 0;
        left: 0;
        font-weight: 600;
        line-height: $measure-large;
        text-align: center;
        height: 70%;

        @include media('<large') {
            line-height: inherit;
            font-size: 14px;
        }
    }

    // Each Event
    .calendar__event {
        display: none;
    }

    .calendar__event:before {
        font-family: 'FontAwesome';
        content: '';
        color: $color-black;
        font-size: 0.625rem;
        vertical-align: top;

        @include media('<large') {
            font-size: 8px;
        }
    }
    .calendar__event:nth-child(2) {
        display: inline-block;
    }

    // Filter for category dropdown
    .calendar__key__wrap {
        position: relative;
        margin-bottom: $measure-xlarge;

        @include media('<=small') {
            margin-bottom: $measure-medium;
        }
    }

    .calendar__key__wrap {
        // re-styling the dropdown arrow
        @include styled-dropdown;
    }
    .calendar__key {
        .filter-box__open {
            display: none;
        }

        .filter-box__float {
            overflow: visible;
        }

        .filter-box__inner {
            padding: 0;
            border: none;
        }
        select {
            @include styled-input-box;
            font-weight: $font-weight-medium;
        }
    }

    /**
    *===================================================================== FROM EVENT-LISTING__AUDIENCE
    */

    .events-listing {
        ul {
            padding: 0 !important;
            margin: $measure-large 0 !important;
            list-style: none !important;
        }

        li {
            display: flex;
            background: $color-white;
            box-shadow: 0 0 1.5rem 0.187rem rgba(0, 0, 0, 0.15);
        }

        li + li {
            margin-top: $measure-large;
        }

        .events-time__wrap {
            background-color: $color-alizarin;
            display: flex;
        }

        time {
            width: 4.5em;
            color: $color-white;

            line-height: 1;
            text-align: center;
            margin: auto auto;

            .events-listing__day {
                display: block;
                font-weight: $font-weight-light;
            }

            .events-listing__month {
                display: block;
                text-transform: uppercase;
                font-weight: $font-weight-medium;
                line-height: $line-height-default;
            }
        }

        .event-listing__details {
            padding: $measure-medium;
            width: 100%;
            display: grid;
            grid-column-gap: 1.5rem;
            grid-template-columns: repeat(10, 1fr);
        }

        .event-listing__details__column {
            grid-column: 1 / 8;

            @include media('<small') {
                grid-column: 1 / -1;
            }
        }

        .event-listing__details__column:last-of-type {
            grid-column: 8 / -1;
            display: flex;
            justify-content: flex-end;

            @include media('<small') {
                grid-column: 1 / -1;
                justify-content: inherit;
            }
        }
        .events-listing__title {
            margin-bottom: $measure-small;
            font-size: $font-size-delta;
            font-weight: $font-weight-medium;
            line-height: 1.5;
            vertical-align: top;
            font-family: $font-family-meta;

            a {
                color: inherit;
                @include multi-line-animation;
                text-decoration: none;

                &:link {
                    border-bottom-width: 0;
                }

                &:hover,
                &:focus,
                &:active {
                    background-color: transparent;
                }
            }
        }

        .events-listing__metadata {
            margin-bottom: $measure-small;
            color: $color-lucifer;
        font-size: 0.875rem;
        font-weight: $font-weight-medium;
        }
        .events-listing__metadata:before {
            content: '\f017';
            font-family: FontAwesome;
            margin-right: $measure-small;
            margin-bottom: $measure-small;
        }

        .events-listing__audience {
            display: inline-block;
            margin-right: $measure-small;
            margin: auto auto 0;
            color: $color-sherpa;
            @include anchor-border-float-in($color-sherpa);

            @include media('<small') {
                margin: inherit;
            }

            &:empty {
                display: none;
            }
        }

        .events-listing__separator {
            text-align: center;
            font-size: 0.8em;
            min-height: $border-width-hairline;
            position: relative;

            &:before {
                content: '';
                background-color: $color-white;
                width: 100%;
                height: $border-width-hairline;
                position: absolute;
                top: 50%;
                left: 0;
                transform: translateY(-50%);
            }

            span {
                background: $color-white-smoke;
                display: inline-block;
                position: relative;
                padding: 0 $measure-medium;
                z-index: 2;

                &:empty {
                    display: none;
                }
            }
        }
    }

    .cass-events__view-month {
        min-height: 2.426em;
        padding-top: 0.357em;
        display: inline-block;

        label {
            width: $border-width-hairline;
            height: $border-width-hairline;
            margin: -$border-width-hairline;
            padding: 0;
            border: 0;
            position: absolute;
            overflow: hidden;
            clip: rect(0, 0, 0, 0);
        }
        select {
            @include styled-input-box;

            padding-right: 1.8rem;
        }

        @include media('<small') {
            width: 100%;
        }
    }
    .cass-events__view-month .date-day__wrap,
    .cass-events__view-month .date-month__wrap,
    .cass-events__view-month .date-year__wrap {
        display: inline-block;
        position: relative;
        margin-right: $measure-medium;

        @include media('<small') {
            margin-right: $measure-small;
        }
    }

    .cass-events__view-month .date-day__wrap:after,
    .cass-events__view-month .date-month__wrap:after,
    .cass-events__view-month .date-year__wrap:after {
        pointer-events: none;
        position: absolute;
        content: '';
        top: 50%;
        right: 1rem;
        margin: -0.312rem 0 0 -0.312rem;
        width: 0;
        height: 0;
        border-style: solid;
        border-width: 0.437rem $border-width-thick 0;
        border-color: #666666 transparent transparent;
    }

    .cass-events__view-month .date-year__wrap {
        @include media('<min') {
            margin-top: $measure-small;
        }
    }

    .cass-events__filter-results {
        width: 100%;
        label + label {
            margin-left: $measure-medium;
        }
    }

    .cass-events__filter-results__close {
        padding: 0.22rem 0.5rem;
        border-bottom-width: 0;
        line-height: 1;
        font-size: 20px;

        margin-left: $measure-medium;
        display: inline;
        background: $color-alizarin;

        .icon-remove:before {
            content: '\f00d';
            font-family: FontAwesome;
        }
    }

    .fade-out {
        opacity: 0;
        pointer-events: none;
        user-select: none;
        animation: fade-out 1s linear;
    }

    @keyframes fade-out {
        0%,
        75% {
            opacity: 1;
        }
        100% {
            opacity: 0;
        }
    }

    .icon-refresh::before {
        content: '\f021';
        font-family: fontAwesome;
    }

    .icon-spin {
        height: 0.9em;
        display: inline-block;
        -webkit-animation: spin 2s infinite linear;
        animation: spin 2s infinite linear;
        font-weight: 400;
        font-style: normal;
        width: auto;
        height: auto;
        font-size: 1.5em;
    }
}

.cass-events-search__footer {
    margin-top: $measure-large;

    &:before {
        background: $color-white-smoke;
        left: 0;
        transform: inherit;
    }
}
