/* Layout styles*/
.bayes-container{
    @include media('<extralarge'){
        padding-left: $measure-medium;
        padding-right: $measure-medium;
    }
}

.bayes-full-width-container{
    @include media('>=extralargePlusSides'){
        margin-left: 5rem;
        margin-right: 5rem;
    }

    @include media('>=extralarge','<extralargePlusSides'){
        margin-left: calc((100% - 1440px)/2);
        margin-right: calc((100% - 1440px)/2);
    }

    .bayes-full-width-container__wrapper{
        max-width: $grid-width-extra-large;
        margin-left: auto;
        margin-right: auto;
        padding-left: $measure-medium;
        padding-right: $measure-medium;
        height: calc(100% + 4rem);
    }
}

@include media('>extralarge') {
    .bayes-programmes{
    
        .grid__8{
            margin-left: auto;
            margin-right: auto;
            margin-top: 5rem;
            margin-bottom: 5rem;
        }
    }
    
    .bayes-feature-content{
        padding-left: 3rem;
        padding-right: 3rem;
    }
}

/* styles */
@import '../components/bayes-carousel/carousel-svg'; /*carousel button svg*/
@import '../components/bayes-home/video-banner'; /*Banner Video*/
@import '../components/bayes-home/spotlight-feature'; /* Spotlight styles */
@import '../components/bayes-home/programme-block'; /* Programmes block styles */
@import '../components/bayes-home/statistic-block'; /* Statistic block styles */
@import '../components/bayes-home/news-style'; /* News styles */
@import '../components/bayes-home/feature-content'; /* Feature content block styles */
@import '../components/bayes-home/social-block'; /* social styles */

/* ease in effect*/
.ease-into-view{
    opacity: 0;
    transition: opacity ease-in 0.3s;
}

.ease-into-view-completed{
    opacity: 1;
}

/* Side bars styles */

.bayes-left-sidebar{
    position: absolute;
    z-index: 2;
    height: 100%;
    width: $measure-medium;
    left: 0;
    top: 0;
    background-color: transparent;
    border-right: solid #dddddd 1px;
    transition: background-color 1s;
}

.bayes-right-sidebar{
    position: absolute;
    z-index: 2;
    height: 100%;
    width: 1rem;
    right: 0;
    top: 0;
    background-color: transparent;
    border-left: solid #dddddd 1px;
    transition: background-color 1s;
}

@include media('>=extralarge','<extralargePlusSides'){
    .bayes-left-sidebar,
    .bayes-right-sidebar{
        width: calc((100% - 1440px)/2);
    }
}

@include media('>=extralargePlusSides'){
    .bayes-left-sidebar{
        width: 5rem;
    }

    .bayes-right-sidebar{
        width: 5rem;
    }
}

.bayes-left-sidebar-scrolled{
    transition: background-color 1s;
    background-color: $home-bg;
}

.bayes-right-sidebar-scrolled{
    transition: background-color 1s;
    background-color: $color-white;
}

/* events styles */

.bayes-events{
    padding-top: 3rem;
    padding-bottom: 3rem;
    padding-left: 3rem;
    padding-right: 3rem;
    @include grid-top;

    .bayes-events__title{
        position: relative;
        width: fit-content;
        align-items: center;
        padding: 0.5rem 2rem;
        margin-bottom: $measure-medium;

        h2{
            line-height: 1;
            margin-bottom: $measure-medium;
            font-size: 2.75rem;
            font-weight: bold;
        }
    }

    a{
        width: fit-content;
    }

    @include media('>=medium'){
        display: flex;

        .bayes-events__wrapper{
            width:33%;
        }

        .feature-events__cards__wrapper{
            width: 65%;
        }

        .feature-events__cards__content{
            width: 50%;
        }
    }

    @include media('<medium'){
        .bayes-events__wrapper{
            margin-bottom: $measure-medium;
        }
    }
}