.anchor-demo-list {
    @extend .default-list;

    li {
        padding: $measure-medium;

        @for $i from 0 through 10 {
            &:nth-child(#{$i + 1}) {
                background-color: mix($color-black, $color-white, $i * 10%);
                color: contrasting-color(
                    mix($color-black, $color-white, $i * 10%)
                );
            }
        }
    }
}

div.widget-123_test_ {
    display: block;
