    border-style: $border-style-default;
    border-color: $color-emperor;
    border-width: 0 0 $border-width-hairline;

    padding-top: $measure-medium;
    padding-bottom: $measure-medium;

    &:hover,
    &[aria-selected='true'] {
        background: $color-gallery;
        color: $color-black;
    }

    &:after {
        @include font-family(icon);
        content: '\f054';
        padding-left: $measure-small-relative;
