@mixin brackets($color: $color-lucifer) {
    @include anchor(reset);
    color: $color;
    line-height: 1;
    vertical-align: bottom;
    padding: 0 0.7em;
    transition: color 0.2s;
    position: relative;
    display: inline-block;
    margin: 0 3px;
    font-weight: $font-weight-bold;

    a {
        color: $color;

        &:after {
            background-color: transparent;
        }
    }

    &:hover {
        color: $color;

        &:before,
        &:after {
            opacity: 1;
            transform: translateX(0);
        }
    }

    &:before {
        left: -0.1em;
        content: '[';
        transform: translateX(-100%);
    }

    &:after {
        right: -0.1em;
        content: ']';
        transform: translateX(100%);
    }

    &:before,
    &:after {
        position: absolute;
        top: -3px;
        font-weight: $font-weight-thin;
        font-size: $font-size-delta;
        line-height: 1;
        opacity: 0;
        transition: opacity 0.2s, transform 0.2s;
    }
}

@mixin anchor-ghost() {
    @include anchor(reset);
    background-color: $transparent;
    color: $color-white;
    display: inline-block;
    padding: $measure-medium;
    border-width: 0;
    border-radius: 0;
    overflow-x: hidden;
    overflow-y: visible;
    cursor: pointer;

    %heading-elements:before {
        @include animation(slide-right-out);
        @include absolute(bottom-left);
        content: '';
        opacity: 0;
        background-color: $color-white;
        width: 100%;
        height: $border-width-default;
        transition: opacity $transition-duration $transition-function
            $transition-duration;
        z-index: z-index(root);
    }

    &:hover {
        %heading-elements:before {
            @include animation(slide-left-in);
            opacity: 1;
            transition: none;
        }
    }

    &:focus {
        outline: $border-width-outline $border-style-default $color-white;
    }

    &:active {
        outline: $border-width-outline $border-style-alternate $color-white;
    }
}

@mixin anchor-ghost-full-highlight() {
    @include anchor(reset);
    background-color: $transparent;
    color: rgb(255, 255, 255);
    display: inline-block;
    overflow-x: hidden;
    overflow-y: visible;
    cursor: pointer;
    border-bottom-width: 1px;

    %heading-elements:before {
        @include animation(none);
        content: '';
        animation: none;
    }

    &:hover {
        background-color: $color-white;
        color: $color-black;
        transition: background-color 1s ease;

        %heading-elements:before {
            @include animation(none);
            transition: none;
            animation: none;
            opacity: 0;
        }
    }

    &:focus {
        outline: none;
    }

    &:active {
        outline: none;
    }

    &:focus {
        outline: $border-width-outline $border-style-default $color-white;
        outline-offset: $border-width-default;
    }

    &:active {
        background-color: $color-white;
        color: $color-black;
        transition: background-color 1s ease;
        outline: $border-width-default $border-style-alternate $color-white;
        outline-offset: $border-width-default;
    }
}

@mixin anchor-left-hand-navigation() {
    @include anchor(reset);
    color: $color-black;
    display: block;
    padding: $measure-medium $measure-medium $measure-small;
    vertical-align: baseline;
    line-height: $line-height-perfect-fifth;

    span {
        color: inherit;
        display: inline-block;
        overflow-x: hidden;

        &:after {
            @include transition(opacity, transform);
            content: '';
            opacity: 0;
            background-color: currentColor;
            width: 100%;
            height: $border-width-default;
            display: block;
            transform: translateX(-100%);
        }
    }

    &:hover,
    &:focus,
    &:active {
        span {
            &:after {
                opacity: 1;
                transform: translateX(0);
            }
        }
    }

    &:focus {
        outline: $border-width-outline $border-style-default $color-black;
    }

    &:active {
        outline: $border-width-default $border-style-alternate $color-black;
    }
}

@mixin anchor-draw-outline($color: $color-white) {
    @include anchor(reset);
    display: inline-block;
    padding: 0;
    border: 0;
    border-radius: 0;
    margin: 0;
    overflow: hidden;

    &,
    &:hover,
    &:focus,
    &:active {
        background-color: $transparent;
        color: $color;
    }

    &:focus {
        outline: $border-width-outline $border-style-default $color-black;
    }

    &:active {
        outline: $border-width-default $border-style-alternate $color-black;
    }

    .text {
        background-color: $transparent;
        color: currentColor;
        display: block;
        padding: $measure-medium $measure-large;
        border: $border-width-thick $border-style-default currentColor;
        border-radius: 0;
        position: relative;
        cursor: pointer;

        &:before,
        &:after {
            content: '';
            background-color: currentColor;
            display: block;
            position: absolute;
            top: -$border-width-thick;
            bottom: -$border-width-thick;
            left: -$border-width-thick;
            right: -$border-width-thick;
            clip-path: polygon(0% 0%);
        }

        &:hover,
        &:focus,
        &:active {
            animation: placehold-line $transition-duration-long linear forwards;

            &:before {
                animation: draw-line $transition-duration-long linear forwards;
            }

            &:after {
                animation: maintain-line $transition-duration-long linear
                    forwards;
            }
        }
    }
}

@mixin anchor-control-button() {
    @include anchor(reset);
    background-color: $color-nobel;

    &:hover {
        background-color: $color-gallery;
    }

    &:active,
    &.active {
        background-color: $color-white;
        &:before {
            border: 2px dotted $color-white;
        }
    }

    &:focus {
        background-color: $color-nobel;
        &:before {
            border: 3px solid $color-gallery;
        }
    }
}

@mixin dropdown {
    color: $color-lucifer;
    background: $color-white;
    border: $border-width-default $border-style-default $color-nobel;

    &:hover {
        background: $color-concrete;
    }

    &:active {
        background: $color-white;
        outline: $border-width-default $border-style-alternate $color-black;
        outline-offset: $border-width-outline;
    }

    &:focus {
        background: $color-white;
        outline: $border-width-outline $border-style-default $color-black;
        outline-offset: $border-width-outline;
    }

    &:-moz-focusring {
        color: $transparent;
        text-shadow: 0 0 0 $color-black;
    }
}

@mixin multi-line-animation {
    @include anchor-reset;
    @include anchor-inline-block;
    color: $color-lucifer;
    display: inline;
    overflow-x: hidden;
    text-decoration: none;
    background-image: linear-gradient($color-black, $color-black);
    background-position: 0% 100%;
    background-repeat: no-repeat;
    background-size: 0% 2px;
    transition: background-size 0.3s;

    &:hover,
    :focus {
        background-size: 100% 2px;
    }

    &:hover {
        /* 
            remove any other transition 
        */
        &:after {
            transform: none;
            opacity: 0;
        }
    }
}

@mixin news-article-anchor($font-color: $color-dove-gray) {
