.slider-news{

    @include media('<=medium'){
        margin-bottom: $measure-medium;
        ul{
            width: 300vw!important;

            li{
                display: inline-block;
                width: 80vw;
            }
        }
        .slider__indicators__wrap{
            width: auto;
            float: left;
            vertical-align: middle;
            margin-left: $measure-small;
        }

        .slider__indicators{
            display: flex;
            vertical-align: middle;
        }
        .slider__indicators__indicator{
            height: $measure-medium;
            width: $measure-medium;
            border-radius: 50%;
            display: inline-block;
            cursor: pointer;
            border: thin solid black;
            margin-right: $measure-medium;
            margin-top: auto;
            margin-bottom: auto;


        }
        .slider__indicators__indicator--active{
            background: $color-alizarin;
            height: 1.25rem;
            width: 1.25rem;
        }

        .news-heading-toggle-link__wrap{
            
            vertical-align: top;
            

            .news-heading-toggle-link{
                margin-bottom: 0;
                margin-left: auto;
                margin-right: 0;

                @include media('<=small'){
                    margin-right: $measure-small;
                }
            }
        }
    }
    
}
