.bayes-feature-content{
    padding-top: 5rem;
    padding-bottom: 5rem;
    background-color: $color-gallery;

    h3{
        font-size: 2rem;
        font-weight: bold;
    }

    .bayes-full-width-container__wrapper{
        > div:nth-child(2),
        > div:nth-child(3){
            margin-top: 3rem;
        }
    }
}

.bayes-feature-content__img-text--image{
    @include media('>between'){
        margin-right: $measure-medium;
    }

    @include media('<=between'){
        margin-bottom: 1rem;
    }
}

.bayes-feature-content__img-text{
    position: relative;
    @include media('>between'){
        display: flex;
        justify-content: space-between;
    }

    @include media('>=extralarge'){
        margin: 1rem 10rem;
    }
}

.bayes-feature-content__img-text--image,
.bayes-feature-content__img-text--text{
    @include media('>between'){
        width: 50%;
    }
}

.bayes-feature-content__img-text--text{
    @include media('>between'){
        width: 50%;
        margin-top: auto;
        margin-bottom: auto;
    }
}

.bayes-feature-content__video{
    margin-left: auto;
    margin-right: auto;
    margin-bottom: $measure-medium;
    margin-top: $measure-medium;
}