.timeline {
    position: relative;
    margin-bottom: $measure-xlarge;
    &:before {
        content: '';
        position: absolute;
        top: 0;
        height: 100%;
        width: 4px;
        background: black;
        left: 50%;
        @include media('<=small') {
            left: 10%;
        }
    }

    .timeline__collection {
        width: 100%;

        &.bounce-in {
            @include media('<=small') {
                animation: timeline-bounce-mobile 1s;
            }
        }

        &.bounce-in:nth-child(odd) {
            @include media('>small') {
                animation: timeline-bounce 0.6s;
            }
        }

        .timeline__collection__icon {
            position: absolute;
            background-color: $color-quill;
            width: 60px;
            height: 60px;
            left: 50%;
            margin-left: -28px;
            border-radius: 50%;
            display: table-cell;
            vertical-align: middle;
            text-align: center;
            @include media('<=small') {
                width: 40px;
                height: 40px;
                left: 10%;
                margin-left: -18px;
            }

            span {
                color: #ffffff;
                font-size: 30px;
                position: relative;
                top: 25%;
                @include media('<=small') {
                    font-size: 20px;
                    top: 10px;
                }
            }
        }

        .timeline__collection__content {
            width: 45%;
            padding: 0 10px 5px 0;
            text-align: right;
            @include media('<=small') {
                position: relative;
                left: 20%;
                width: 80%;
                text-align: left;
            }
            li {
                @include media('<=small') {
                    text-align: left;
                }
            }
        }

        &.bounce-in:nth-child(even) {
            @include media('>small') {
                animation: timeline-bounce-inverse 0.6s;
            }
        }

        &:nth-child(even) .timeline__collection__content {
            position: relative;
            text-align: left;
            left: 55%;
            padding: 0 0 5px 10px;
            @include media('<=small') {
                left: 20%;
                padding: 0;
            }
        }
    }
    .timeline__collection--hide {
        visibility: hidden;
    }
}

.scp--masters {
    .timeline__collection {
        .timeline__collection__icon {
            background-color: $color-allports;
        }
    }
}

