.cass-knowledge-search {
    position: relative;
    margin-bottom: $measure-large;

    .news-finder__form .news-articles-list {
        display: block;
        width: 100%;
        padding-left: $measure-medium;

        li {
            display: block;
            margin-bottom: $measure-large;
            width: 100%;
            min-height: 16.125rem;
        }
    }
    .news-card {
        position: relative;
        overflow: hidden;
        display: grid;
        grid-template-columns: repeat(16, 1fr);
        grid-column-gap: 0.8rem;
    }

    .underline-transition__title {
        @include multi-line-animation;
        @include anchor-inline-block; 
        display: inline;

        &:after{
            display: none;
        }
    }

    .news-card .image-credit {
        left: 0;
        right: inherit;
    }

    .news-articles--landing {
        .cass-knowledge-listing__item__text {
            @include media('<between') {
                position: relative;
                display: block;
                width: 100%;
            }

            @include media('>between') {
                width: 84%;
            }

            @include media('>large') {
                width: 75%;
            }

            @include media('>medium') {
                position: absolute;
                display: inline-block;
            }

            .feature-news__card__body__published {
                margin-top: $measure-medium;
                font-weight: $font-weight-medium;
                color: $color-lucifer;
            }
        }

        .cass-knowledge-listing__item {
            @include media('<between') {
                position: relative;
                display: block;
                width: 100%;
            }

            img {
                @include media('<between') {
                    width: 100%;
                }

                @include media('>between') {
                    width: 51%;
                }

                @include media('>medium') {
                    position: absolute;
                    display: inline-block;
                }

                @include media('>large') {
                    width: 42%;
                }
            }

            .image-credit {
                left: 0;
                right: unset;
                display: flex;
            }

            .image-credit__button {
                float: none;
            }
        }

        .cass-knowledge-listing__item__title{
            font-family: $font-family-meta;
        }
    }

    .news-card-categories > * {
        display: inline-block;
    }

    .news-card-series {
        font-size: 0.9rem;
        padding-left: $measure-small;
        padding-right: $measure-small;
        background: none;
        @include news-article-anchor;

    }
    .news-card-topic__wrap {
        display: inline-block;
        margin-right: $measure-medium;
    }

    .news-card-topic {
        color: $color-emperor;
        margin-top: $measure-small;
        display: inline-block;
        font-size: 0.9rem;
        background: none;
    }
}

.mailing-list-card__content {
    .cta {
        width: auto;
        padding: $measure-medium;
    }
}
