.promo-cta {
    padding: 10px 30px;

    text-align: center;

    h2 {
        font-weight: $font-weight-medium;
        margin-top: 1rem;
        display: inline-block;
        padding: 0 15%;

        &:after {
            content: '';
            display: block;
            width: 80px;
            height: 3px;
            margin: 20px auto;
            max-height: 240px;
        }
    }

    &.promo-cta--small {
        padding: 5px 15px;
        h2 {
            font-size: $font-size-delta;
            padding: 0;

            &:after {
                margin: 10px auto;
            }
        }
    }
}

.promo-cta__button {
    padding: $measure-small $measure-medium;
    width: 160px;
    text-align: left;
    position: relative;

    .promo-cta__button__text {
        display: inline-block;
    }
    .promo-cta__button__icon {
        display: inline-block;
        position: absolute;
        right: $measure-medium;
        top: 12px;
    }
}

.promo-cta__button--center {
    text-align: center;
}

.promo-cta--white {
    background-color: white;

    h2:after {
        background-color: $color-alizarin;
    }
}

.promo-cta--white-orange {
    background-color: white;

    h2:after {
        background-color: $color-orange;
    }
}

