/*Animation keyframes*/

@keyframes revealSlideUp {
    0% {
        opacity: 0;
        transform: translateY(2rem);
    }
    100% {
        opacity: 1;
        transform: translateY(0);
    }
}

@keyframes reveal {
    0% {
        transform: scaleY(1);
    }
    100% {
        transform: scaleY(0);
    }
}

@keyframes lines {
    0% {
        transform: scaleX(0);
    }
    50% {
        transform: scaleX(1);
    }
    100% {
        transform: scaleX(0);
    }
}
  
@keyframes line-left {
    0% {
        transform-origin: 100% 50%;
    }
    50%, 100% {
        transform-origin: 0% 50%;
    }
}
  
@keyframes line-right {
    0% {
        transform-origin: 0% 50%;
    }
    50%, 100% {
        transform-origin: 100% 50%;
    }
}

@keyframes bracket {
    0% {
        transform: scaleY(0);
    }
    100% {
        transform: scaleY(1);
    }
}
  
@keyframes bracket-line {
    0% {
        transform: scaleX(0);
    }
    100% {
        transform: scaleX(1);
    }
}

.bayes-home{
    position: relative;

