.news-article {
    blockquote {
        &:before {
            background-color: $color-alizarin;
        }

        p {
            font-weight: 400;
        }
    }
}

.news-article__related-course {
    border-bottom: $border-width-hairline solid $color-nobel;

    &:first-child {
        border-top: none;
    }

    .related-course__title {
        width: 60%;

        @include media('>large') {
            width: 65%;
        }
    }

    .icon {
        float: right;
        margin-top: 0.1rem;
    }
}

.news-article__related-course__list {
    border-top: $border-width-hairline solid $color-nobel;
}

.news-article__stretched-title {
    @include media('>medium') {
        margin: 0 10rem;
        text-align: center;
    }

    h1 {
        margin-top: $measure-medium;

        @include media('>medium') {
            margin-top: 0;
        }
    }
}

.news-article__news-standfirst {
    @include standfirst;

    p {
        @include standfirst;
    }

    // enforce 300 to preserve design
    strong {
        font-weight: 300;
    }
}

.news-article__date-published {
    margin: 1.5rem 0;
}

.feature-news__card {
    .item__date {
        color: $color-lucifer;
        font-weight: 600;
    }

    a.news-article-tag {
        font-size: 0.875rem;
    }

    &:nth-child(5),
    &:nth-child(6) {
        display: none;
    }

    .news__image-credit {
        padding: .2rem .4rem;
        bottom: unset;
        top: 0;
        right: 0;
        z-index: 1;

        a {
            font-size: 1em;
        }
    } 
}

.feature-news__card--follow {
    position: relative;
}

.news-article__article-meta-data {
    border-top: $border-width-hairline solid $color-nobel;
    border-bottom: $border-width-hairline solid $color-nobel;
    padding: $measure-medium 0 $measure-large 0;

    time {
        display: block;
    }

    .news-article-tag {
        margin-bottom: $measure-medium;

        @include media('>medium') {
            margin-bottom: 0;
        }
    }
}

.featured-cass-experts__profile {
    display: flex;
    margin-bottom: 1.5rem;

    &:last-child {
        margin-bottom: 0;
    }

    i {
        margin-right: 0.4rem;
    }
}

.featured-cass-experts__profile__photo {
    width: 8rem;

    @include media('>small') {
        width: 5rem;
    }

    margin-right: $measure-medium;
}

.featured-cass-experts__press-officer-name {
    a {
        font-size: 1.2rem;
    }
}

.news-article__featured-cass-experts {
    border-top: $border-width-hairline solid $color-nobel;
    border-bottom: $border-width-hairline solid $color-nobel;
    padding: 1.2rem 0 $measure-large 0;

    h2 {
        text-align: left;
        font-size: 1.44rem;
    }
}

.featured-cass-experts__press-officer-jobtitle {
    margin: 0.5rem 0 0.3rem 0;
}

.news-article__tags {
    margin: 3rem 0;
    color: $color-dove-gray;

    a {
        color: $color-dove-gray;
        border-bottom-color: $color-dove-gray;

        &:hover {
            transition: color 0.2s ease;
            background: none;
        }
    }
}

.news-article__anchor {
    @include anchor-border-float-in;
    padding: 0.6rem;
    margin: 0.4375rem $measure-medium 0 0;
    color: $color-dove-gray;
    font-weight: 600;
    font-size: 0.875rem !important;
    margin-right: $measure-medium;
    border-bottom: none;
    display: inline-block;
    background-image: none !important;
    background-size: unset !important;

    @include topic-anchor-border-float-in;

    &:hover {
        background: none;
        color: $color-dove-gray;
    }

    &:last-child {
        margin-right: 0;
    }
}

.news-article__press-officer {
    display: block;
    margin-top: $measure-medium;
}

.news-article__social-icons-list {
    text-align: center;
    margin-top: $measure-large;
    margin-bottom: $measure-large;

    a {
        border-bottom: none;
        transition: none;
        color: $color-white;
        background: $color-black;
        border-radius: 50%;
        width: 2.5rem;
        height: 2.5rem;
        display: inline-block;
        transition: background-color 0.6s, transform 0.6s;

        &:hover {
            transform: scale(1.2);
            background-color: $color-alizarin;
        }

        span {
            display: flex;
            font-size: 1.2rem;
            height: 100%;
            width: 100%;
            align-items: center;
            justify-content: center;
        }
    }

    li {
        display: inline;
        margin-right: $measure-small;

        &:last-child {
            margin-right: 0;
        }
    }
}

.news-article__press-body {
    margin-top: $measure-xlarge;
}

.related-courses-list,
.related-research-papers {
    h2 {
        text-align: left;
    }
}

.related-courses-section,
.related-research-papers-section {
    background-color: transparent;

    @include media('>medium') {
        position: relative;

        &:after {
            content: '';
            background-color: $color-white-smoke;
            position: absolute;
            top: 0;
            left: 0;
            margin-left: 9%;
            width: 82%;
            height: 100%;
            z-index: 1;
        }

        .container {
            z-index: 2;
        }
    }
}

.related-stories-section {
    margin-top: $measure-large;

    .grid {
        justify-content: center;
    }

    h2 {
        margin-bottom: $measure-medium;
    }

    .feature-news__card {
        @include media('<small') {
            width: 80%;
        }

        @include media('>medium') {
            width: 25%;
        }
    }
}

.related-research-paper {
    width: 100%;
    border-top: $border-width-default solid $color-gallery;
    border-bottom: $border-width-default solid $color-gallery;

    a {
        padding: $measure-small;
        display: block;
        border-bottom: none;
        display: flex;

        &:hover {
            .icon {
                color: $color-white;
            }
        }
    }

    .icon {
        margin-left: auto;
        color: $color-black;
    }
}

.related-research__title {
    vertical-align: middle;
    display: inline-block;
    width: 90%;
}

.news__image-credit {
    bottom: $measure-small;
    right: $measure-small;
    position: absolute;
    color: $color-white;
    padding: $measure-small;
    background: rgba($color-black, 0.8);
    display: flex;
    align-items: center;

    @include media('>medium') {
        bottom: 5rem;
        right: $measure-medium;
    }
}

.image-credit__text {
    padding: .35em;

    i {
        font-size: 1.1rem;
    }

    a {
        color: $color-gainsboro;
        border-bottom-color: $color-gainsboro;

        &:hover {
            color: $color-white;
            transition: color 0.2s ease;
            background: none;
        }
    }
}

.news__banner-container {
    height: 42vh;
    position: relative;

    @include media('>small') {
        height: 26vh;
    }

    @include media('>medium') {
        height: 47vh;
    }

    @include media('>extralarge') {
        height: 44vh;
    }
}

.container__more-news {
    display: block;
    margin-top: $measure-small;

    @supports (display: flex) {
        display: flex;
        flex-wrap: wrap;
        justify-content: flex-end;
    }

    a {
        @include anchor(skate);
        float: right;
    }

    i {
        margin-left: $measure-medium;
    }
}

.feature-news__card--landscape {
    margin: 0 $measure-small $measure-large $measure-small;
    position: relative;
    border: $border-width-hairline solid $color-gainsboro;

    @include media('>large') {
        width: 40%;
        margin: 0 $measure-large $measure-large 0;
        border: none;
    }

    .feature-news__card__body {
        background-color: #fff;
        padding: $measure-large;

        @include media('>small') {
            position: absolute;
            right: 0;
            top: 0;
            width: 70%;
            height: 95%;
        }
    }

    &:nth-child(2),
    &:nth-child(4) {
        @include media('>large') {
            margin-right: 0;
        }
    }

    .feature-news__card__body__title {
        a {
            font-size: 1.3rem;
            font-weight: 600;
            border-bottom: none;
            display: block;
            transition: none;
            background: none;
            color: $color-lucifer;
            margin-bottom: $measure-medium;
            text-decoration: underline;

            @include media('>extralarge') {
                font-size: 1.5rem;
            }
        }
    }

    .feature-news__topic {
        color: $color-lucifer;
        font-weight: 600;
        margin-bottom: $measure-medium;
    }

    .feature-news__card__body__published {
        color: $color-lucifer;
        margin-bottom: 0.6rem;
    }

    .item__date {
        font-size: 1.2rem;
    }
}

.container__landscape-cards {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

$cass-anchors: business-and-finance, finance, management, entrepreneurship,
    cass-community, actuarial-science;

$cass-anchor-colors: $finance, $finance, $management, $entrepreneurship,
    $community, $actuarial-science;

@each $current-anchor in $cass-anchors {
    $i: index($cass-anchors, $current-anchor);

    a.anchor--#{nth($cass-anchors, $i)} {
        color: nth($cass-anchor-colors, $i);

        &:hover {
            color: nth($cass-anchor-colors, $i);

            &:after {
                border-color: nth($cass-anchor-colors, $i);
            }
        }
    }
}

// Grid-column rules for featured news card layouts
.grid {
    &[data-layout='news-events-cards'] {
        .feature-news__cards {
            .container {
                .grid {
                    &:before {
                        content: none;
                    }
                    display: grid;
                    grid-column: 1/-1;
                    @include media('<large') {
                        grid-template-columns: repeat(2, 1fr);
                    }
                    @include media('<between') {
                        grid-template-columns: repeat(1, 1fr);
                    }
                    grid-template-columns: repeat(3, 1fr);
                    grid-column-gap: 1.5rem;
                    .feature-news__card {
                        width: 100%;
                    }
                }
            }  
        }
    }
}