.cass-experts {
    padding-top: $measure-large;
    @include media('>medium') {
        padding-top: $measure-enormous;
    }

    .fa {
        margin-right: 1rem;
        width: 20px;
    }

    .standfirst {
        text-align: left;
    }

    .contact-list {
        margin-bottom: $measure-large;
    }

    .academic-details__content {
        ol,
        ul {
            margin-left: 0;

            li {
                margin-bottom: $measure-large;
            }
        }

        cite {
            font-style: normal;
        }

        .expertise-list {
            float: left;
            margin-bottom: $measure-large;

            li {
                border: 1px solid $color-black;
                float: left;
                margin-bottom: $measure-small;
                margin-right: $measure-small;
                padding: $measure-small;
            }

            & + h4 {
                clear: left;
            }
        }

        #favourites li {
            background-color: $color-concrete;
            padding-bottom: $measure-xlarge;
            padding-left: $measure-small;
            padding-right: $measure-small;
            padding-top: $measure-small;
            position: relative;

            &:after {
                bottom: $measure-small;
                color: $color-lucifer;
                content: 'Featured publication';
                position: absolute;
                right: $measure-small;
            }
        }
    }
}
