/* start 
News events landing overrides */

.cass-news-search__wrap .cass-news-search {
    position: static;
    top: 0;
}

/* end  
News events landing overrides */

.cass-news-search {
    position: relative;
    margin-bottom: $measure-large;

    .news-navigation {
        display: block;
        width: 100%;
    }
    #managed-form-container {
        width: 100%;
    }

    .news-finder__query {
        width: 100%;

        h2 {
            font-size: $font-size-gamma;
        }
    }

    .news-finder__query .query-box {
        position: relative;
        margin-bottom: $measure-large;
    }
    .news-finder__query button {
        background: $color-alizarin;
        color: $color-white;
        height: 100%;
        position: absolute;
        right: 0;
        padding: $measure-small $measure-medium;
    }

    .news-finder__query .filter-box {
        @include media('<between') {
            border: $border-width-hairline $border-style-default $color-quill;
            &:focus-within {
                outline: $border-width-outline $border-style-alternate
                    $color-black;
            }
        }
    }

    .news-finder__query .filter-box__inner {
        padding: 0;
        @include media('<between') {
            display: none;
        }
    }

    .filter-box__float--open .filter-box__inner {
        @include media('<between') {
            display: inline;
            border: none;
        }

        .row-fluid {
            @include media('<between') {
                display: grid;
                grid-template-columns: repeat(4, 1fr);
                grid-column-gap: 0.8rem;
                padding: $measure-small;

                > div {
                    grid-column: 1 / -1;
                }

                .filter-box__filter--M {
                    grid-column: 1 / 3;
                }

                .filter-box__filter--m {
                    grid-column: 3 / -1;
                }
            }
        }
    }

    .filter-box__open {
        background: $color-white;
        color: $color-alizarin;
        border: none;
        border-bottom: $border-width-hairline $border-style-default $color-quill;

        &:active,
        &:focus {
            outline: none;
        }
    }

    .filter-box__open h2 {
        display: flex;
        flex-direction: row;
        font-size: $font-size-delta;
    }

    .filter-box__open h2:before {
        content: '\f0b0';
        font-family: FontAwesome;
        margin-right: $measure-medium;
    }

    .filter-box__open h2 > div {
        width: 100%;
        display: flex;
    }

    .filter-box__open .icon-angle-up,
    .filter-box__open .icon-angle-down {
        margin-left: auto;
        color: $color-black;
        font-size: $font-size-gamma;
    }

    .filter-box__open .icon-angle-up:before {
        content: '\f106';
        font-family: FontAwesome;
    }

    .filter-box__open .icon-angle-down:before {
        content: '\f107';
        font-family: FontAwesome;
    }

    .news-finder__query .filter-box__float {
        overflow: visible;
        margin-top: $measure-medium;
    }

    .news-finder__query #query-box__query {
        @include styled-input-box();
        border-color: $color-quill;
    }

    .news-finder__query .icon-search:before {
        font-family: 'FontAwesome';
        content: '\f002';
    }

    .news-finder__query .filter-box__filter__content {
        @include styled-dropdown;
        select {
            @include styled-input-box;

            font-weight: $font-weight-medium;
        }
    }

    .news-finder__main {
        display: flex;
        flex-direction: row;
        width: 100%;
    }

    .news-finder__main__left {
        width: 33.333%;
        padding: $measure-medium;
        @include media('<medium') {
            width: 100%;
        }
    }
    .news-finder__main__right {
        width: 66.667%;

        padding: $measure-medium;
        position: relative;
        @include media('<medium') {
            width: 100%;
        }
    }
    .news-finder__main__right:before {
        position: absolute;
        top: -$measure-large;

        -webkit-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;
        -webkit-touch-callout: default;
        -webkit-touch-callout: none;
        content: '';
        width: 200vw;
        height: calc(100% + 2rem);
        pointer-events: none;
        background: $color-white-smoke;
        left: 0;
    }

    .news-heading__wrap {
        display: flex;
        margin-bottom: $measure-medium;
        width: 100%;

        @include media('<between') {
            flex-direction: column;
        }
    }
    .news-heading {
        margin-bottom: $measure-medium;
        h2 {
            @include events-news-listings-heading();
            padding: 0 $measure-medium;
        }

        @include media('<between') {
            margin-right: auto;
        }
    }
    .news-heading-toggle-link__wrap {
        display: block;

        @supports (display: flex) {
            display: flex;
            flex-direction: row-reverse;
        }
    }
    .news-heading-toggle-link {
        float: right;
        margin-left: auto;
        margin-top: auto;
        margin-bottom: auto;
        border-bottom-width: 0;
        color: $color-black;
        @include anchor(skate);

        @include media('<between') {
            display: block;
            margin-left: inherit;
            margin-bottom: $measure-medium;
            margin-right: auto;
        }

        @include media('<min') {
            display: none;
        }
    }

    .news-listing__heading__wrap {
        margin-bottom: $measure-large;
        position: relative;
    }

    .news-listing__heading__before {
        display: inline;
    }

    .news-listing__heading {
        @include events-news-listings-heading();

        h2 {
            padding: 0 $measure-medium;
            display: inline;
        }
    }

    .cass-news-results__close {
        padding: 0.22rem 0.5rem;
        border-bottom-width: 0;
        line-height: 1;
        font-size: $font-size-delta;
        color: $color-white;
        margin-left: $measure-medium;
        display: inline;
        background: $color-alizarin;

        .icon-remove:before {
            content: '\f00d';
            font-family: FontAwesome;
        }
    }

    .news-finder__form {
        width: 100%;
    }
    .news-finder__form .news-articles-list {
        display: flex;
        flex-direction: row;

        li {
            @include media('<large') {
                width: 50%;
            }

            @include media('<small') {
                width: 100%;
            }
        }
    }

    .feature-news__card {
        width: 100%;
        position: relative;
    }

    .feature-news__card__body .item__date {
        display: block;
        margin-bottom: $measure-small;
    }

    .feature-news__card__body .news-card-topic {
        color: $color-sherpa;
        font-size: $font-size-small;
        display: inline-block;
        height: 100%;
        background-image: none;
    }

    .feature-news__card__body__published {
        margin-bottom: 0;
    }

    ///////////////////////// not all image credit code were copied from cityR

    .image-credit {
        color: $color-gainsboro;
        position: absolute;
        top: 0;
        right: 0;
        z-index: 1;
        background-color: #222;
        padding: .2em;
        display: flex;
        align-items: center;

        &[aria-expanded='true'] {
            background-color: #222;
        }

        .news-card & {
            z-index: 4;
        }

        .image-credit__button {
            order: 1;
            color: $color-white;
            position: relative;
            padding: .4em .6em;

            &:hover {
                background-color: #333;
            }

            &:focus {
                outline: $border-width-hairline $border-style-default $color-black;
            }

            &:active {
                outline: $border-width-default $border-style-alternate $color-black;
            }

            .icon-info-sign {
                color: inherit;
                font-size: 1em;
                font-family: 'FontAwesome';
            }

            .icon-info-sign::before {
                content: '\f05a';
            }
        }

        .image-credit__text {
            line-height: 1.5;
            font-weight: 600;

            a {
                text-decoration: none;
                font-size: 1em;

                &:hover,
                &:focus {
                    border-bottom-color: $color-white;
                }

                &:active {
                    border-bottom-color: transparent;
                }
            }
        }
        .image-credit__button[aria-pressed='false'] {
            & + .image-credit__text {
                @include visually-hidden;
            }
        }
    }
    .image-credit--bottom-right {
        bottom: 0;
        top: auto;
    }
    .image-credit--top-left {
        left: 0;
        right: auto;

        .image-credit__button {
            float: left;
        }
    }
    .news-finder__bottom {
        margin-top: $measure-xlarge;
        padding: $measure-xlarge;
        background: $color-prussian;

        h2 {
            color: $color-white;
            text-align: center;
            margin-bottom: $measure-xlarge;
        }

        ul {
            text-align: center;
            margin: 0;

            li {
                list-style: none;
                margin: $measure-small;
                display: inline-block;

                a {
                    text-decoration: none;
                    border: $border-width-hairline $border-style-default
                        $color-white;
                    padding: $measure-small;
                    color: $color-white;
                    display: block;
                }
            }
        }
    }
}

.cass-news-search__footer,
.cass-knowledge-search__footer {
    background: $color-white-smoke;
}

.cass-knowledge-listing__item--last2 {
    .news__image-credit {
        left: 0;
    }
}