.promo-strap{
    background-color: $color-yellow;
    color: $color-black;
    padding-top: $measure-medium;
    padding-bottom: $measure-medium;


    .promo-strap__content{
        @include grid-top;
    }

    .promo-strap__content{
        justify-content: center;
        text-align: center;
        font-weight: 900;

        p{
            margin: initial;
        }

        a{ 
            @include anchor-reset();
            text-decoration: underline;
            color: $color-black;

            &:focus{
                outline: $border-width-hairline $border-style-default $color-black;
            }

            &:active{
                outline: $border-width-hairline $border-style-alternate $color-black;
            }
        }
    }
}