.site-search {
    @include grid-column-base;

    text-align: right;
    padding-right: $grid-padding-half;

    @supports (display: flex) {
        align-self: center;
    }

    width: grid-column-width(1);

    @include media('<=between') {
        width: grid-column-width(2);
    }

    @include media('>large') {
        width: grid-column-width(2);
    }
}

.site-search__container__toggle-label {
    @include anchor(outline);
    cursor: pointer;

    padding: $measure-medium;
    @include media('>medium') {
        padding: $measure-small;
    }

    &:before {
        @include font-family(icon);
        content: $fa-var-search;
        font-size: $font-size-delta;
    }
}

.site-search__toggle,
.site-search__form {
    display: none;
}

.site-search__container {
    position: relative;
}

.site-search__form {
    position: absolute;
    right: 0;
    top: calc(100% + #{$measure-medium * 2.25});
    @include media('>small') {
        top: calc(100% + #{$measure-small});
    }
    width: auto;
    height: $measure-xlarge;
    text-align: right;
    white-space: nowrap;
    z-index: z-index(navigation);
}

.site-search__button,
.site-search__input {
    vertical-align: top;
}

.site-search__input {
    font-family: $font-family-default;
    font-size: $font-size-default;
    font-weight: $font-weight-default;
    height: $measure-xlarge;
    margin: 0;
    padding: $measure-small;
    width: calc(#{$grid-width-min} - #{2 * $grid-padding + $measure-xlarge});
}

.site-search__button {
    @extend .button--icon;
    width: $measure-xlarge;
    height: $measure-xlarge;

    span:before {
        @include font-family(icon);
        content: $fa-var-search;
        font-size: $font-size-delta;
        height: 100%;
        line-height: 100%;
    }
}

.site-search__toggle:checked + .site-search__container {
    .site-search__form {
        display: block;
    }

    .site-search__container__toggle-label:before {
        content: $fa-var-close;
    }
}

.search-page {
    margin-bottom: $measure-medium;

    .site-search__form {
        display: block;
        margin-bottom: $measure-small;
        margin-left: $measure-small;
        position: relative;
        text-align: left;
        z-index: z-index(default);
    }
}

.search-results__list {
    margin-left: $measure-small;
    margin-top: $measure-small;
}

.best-bet {
    background: $color-gallery;
    margin-left: $measure-small;
    margin-top: $measure-medium;
    padding: $measure-small;
    position: relative;
}

.best-bet__title,
.search-result__title {
    font-size: $font-size-gamma;
    margin-bottom: $measure-small;
}

.best-bet__title {
    @include media('<=large') {
        padding-top: $measure-medium + $measure-small;
    }
}

.search-result__url {
    font-size: $font-size-small;
    font-style: italic;
}

.best-bet__url {
    margin-bottom: $measure-small;
    cite {
        font-size: $font-size-small;
        font-style: italic;
    }
}

.best-bet__label {
    top: $measure-medium;
    font-size: $font-size-small;
    font-style: italic;
    position: absolute;
    right: $measure-medium;
}

.search-results__count {
    margin-left: $measure-small;

    span {
        font-weight: $font-weight-medium;
    }
}
