.feature-news {
    position: relative;
}

.feature-news__card {
    @include grid-column-map(
        (
            'small': 6,
            'large': 6,
        )
    );
}

.feature-news__card:nth-child(2) {
    @include grid-column-map(
        (
            'small': 6,
            'large': 3,
        )
    );
}

.feature-news__card:nth-child(3) {	
    @include grid-column-map(	
        (	
            'small': 12,	
            'large': 3,	
        )	
    );	
}

.feature-news__card--main,
.feature-news__card--follow {
    .feature-news__card__body {
        box-shadow: 0 0 1.5rem 3px rgba(0, 0, 0, 0.15);
    }
}

.feature-news__card--main {
    position: relative;
}

.feature-news__card--main,
.feature-news__card--follow {
    img {
        width: 100%;
    }

    .feature-news__card__body {
        background-color: white;
        position: relative;
        top: -40px;
        width: 90%;
        padding: $measure-medium;
    }

    .feature-news__card__body__title {
        font-family: $font-family-meta;
        a{
            @include multi-line-animation;
        }
    }
}

.feature-news__card {
    a {
        
        font-size: $font-size-delta;
        font-weight: $font-weight-medium;

        @include anchor-inline-block; 
        display: inline-block;
    }
}

.feature-news__card--latest {
    padding-left: 20px;

    .feature-news__card__body {
        border-top: 1px solid $color-lucifer;

        @include media('>medium') {
            padding: 20px 0 60px;
            border-bottom: 1px solid $color-lucifer;
        }

        &:before {
            content: '';
            display: block;
            width: 40px;
            height: 4px;
            background-color: $color-lucifer;
            position: relative;
            left: 0;
            top: -20px;
        }
    }
}

.feature-news__link-more {
    a {
        @include anchor(skate);
        font-weight: $font-weight-default;
    }

    position: relative;
    margin: 40px 0 0;
    float: right;

    @include media('>large') {
        margin: 0;
    }
}

.item__date {
    font-size: $font-size-small;
    display: block;
}

.feature-news__card__body__topic {
    padding: 0.6rem;
    margin: 0.4375rem $measure-medium 0 0;
    color: $color-dove-gray;
    font-weight: 600;
    font-size: 0.875rem !important;
    margin-right: $measure-medium;
    border-bottom: none;
    display: inline-block;
    background-image: none !important;
    background-size: unset !important;
    background: none;

   
}

.feature-news__card__body__topic--actuarial-science {
    @include anchor-border-float-in($actuarial-science);
    @include news-article-anchor($actuarial-science);
}

.feature-news__card__body__topic--all {
    @include anchor-border-float-in($color-black);
    @include news-article-anchor($color-black);
}

.feature-news__card__body__topic--finance {
    @include anchor-border-float-in($finance);
    @include news-article-anchor($finance);
}

.feature-news__card__body__topic--management {
    @include anchor-border-float-in($management);
    @include news-article-anchor($management);
}

.feature-news__card__body__topic--entrepreneurship {
    @include anchor-border-float-in($entrepreneurship);
    @include news-article-anchor($entrepreneurship);
}

.feature-news__card__body__topic--community {
    @include anchor-border-float-in($community);
    @include news-article-anchor($community);
}

.feature-news__card--latest
    > .feature-news__card__body:not(.feature-news__card__body--slider-dots) {
    .feature-news__card__slider__item:not(:first-child) {
        display: none;
    }
}

.feature-news__card--latest
    > .feature-news__card__body.feature-news__card__body--slider-dots {
    position: relative;

    &:before {
        display: none;
    }

    .slick-dots {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 0;

        display: flex;
        flex-direction: row;

        > li {
            flex: 1;
            display: inline-block;
            background-color: transparent;
            height: auto;

            button {
                display: block;
                width: 100%;
                height: 4px;
                padding: 0;
                margin: 0;
                border: none;

                background-color: transparent;
                &:hover {
                    background-color: #aaaaaa;
                }
            }
            &.slick-active button {
                background-color: #666666;
            }
        }
    }
}
