////
/// Accordion
/// @group components
////

$icon-size: 0.8rem;
$header-line-height: 1.7rem;

.js {
    .accordion__content__heading-anchor {
        &.inactive + div {
            @include visually-hidden;
        }
    }

    .accordion__content__heading {
        @include visually-hidden;
    }
}

.accordion__content__heading-anchor {
    &:focus {
        outline: 0;
        margin-right: 0;
        bottom: $border-width-outline;
    }
}

.accordion__content__heading-anchor.inactive {
    &:focus {
        margin: 0;
        outline: $border-width-outline $border-style-default $color-black;
        border: 0 $border-style-default $transparent; // Safari fix
    }
}

.accordion__content__heading-anchor.active {
    &:focus {
        outline: $border-width-outline $border-style-default $transparent;
    }
}

.accordion__content__heading-anchor[aria-expanded='true'] {
    &:focus {
        margin: 0;
        outline: $border-width-outline $border-style-default $transparent;
    }
}

.accordion {
    margin-bottom: $measure-large;
    margin-top: $measure-large;
}

.accordion__content__heading-anchor__icon {
    @extend .fa;
    float: right;

    .accordion__content__heading-anchor.inactive & {
        @extend .fa-plus;
        font-size: $icon-size;
        line-height: $header-line-height;
    }

    .accordion__content__heading-anchor.active & {
        @extend .fa-minus;
        font-size: $icon-size;
        line-height: $header-line-height;
    }
}

.accordion__content__heading-anchor.active {
    border: $border-width-default $border-style-alternate $color-black;
    border-bottom: 0;
    margin: 0 0 $border-width-default;
    color: $color-black;
    text-decoration: underline;
    border-bottom: $border-width-hairline $border-style-default $color-gallery;
    .accordion__content__heading-anchor__label {
        text-decoration: underline;
    }
}

.accordion__content__heading-anchor {
    // @include anchor(activable); // TODO: This does nothing until added to anchor mixins
    @include anchor(activable-stacked-normal--transparent);
    h2,
    h3,
    h4 {
        margin: 0;
    }
    padding-left: $measure-medium;
    padding-right: $measure-medium;
    color: $color-endeavour;
    border-bottom: $border-width-hairline $border-style-alternate $color-nobel;
    background: $color-white;
    &:last-of-type {
        border-bottom: $border-width-hairline $border-style-alternate
            $color-nobel;
    }
    &:active {
        outline: $border-width-default $border-style-alternate $color-black;
    }
    &:hover {
        background: $color-white-smoke;
        text-decoration: underline;
    }
}

.accordion__content__heading-anchor__label {
    display: inline-block;
    width: 90%;
    line-height: $header-line-height;
    &:hover {
        text-decoration: underline;
    }
}

.accordion__content__group {
    padding: $measure-medium;
    border: $border-width-default $border-style-alternate $color-black;
    border-top: 0;
}

.accordion.accordion--simple {
    // border-bottom: $border-width-default $border-style-alternate $color-quill;
    margin-bottom: $measure-large;

    .accordion__content__heading-anchor {
        @include anchor(activable-stacked-simple);
        padding-left: 0;
        outline: $border-width-outline $border-style-default $transparent;
        padding: $measure-medium;

        .accordion__content__heading-anchor__label {
            display: inline;
        }

        &.active {
            outline: 0;
            &:focus {
                outline: 0;
            }
        }

        &:focus {
            outline: $border-width-outline $border-style-default $color-black;
            top: 0;
        }

        .accordion__content__heading-anchor__icon {
            @extend .fa;
            float: right;
            margin-right: $measure-medium;
            line-height: $header-line-height;
        }

        &.inactive {
            .accordion__content__heading-anchor__icon {
                @extend .fa-chevron-right;
            }
        }

        &.active {
            .accordion__content__heading-anchor__icon {
                @extend .fa-chevron-down;
            }
        }
    }

    .accordion__content__heading-anchor.active {
        border-bottom: $border-width-hairline $border-style-default
            $color-gallery;
        background: $color-white;
    }

    .accordion__content__group {
        border-top: 0;
        padding: $measure-medium;
        background: $color-white;
    }
}
