////
/// Header
/// @group layout
////

.header {
    @include grid-top;
    padding-top: calc(#{$measure-large} + 10px);
    position: relative;

    @include media('<=between') {
        padding-top: 10px;
    }

    @include media('>between') {
        padding-top: calc(1rem + 10px);
    }

    @include media('>medium') {
        padding-top: calc(2rem + 10px);
    }

    &:before {
        content: '';
        background-color: $color-black;
        height: 10px;
        display: block;
        position: absolute;
        top: 0;

        @include media('>small') {
            left: $grid-padding;
            right: $grid-padding;
        }

        @include media('<=small') {
            left: $grid-padding-half;
            right: $grid-padding-half;
        }
    }
}

.header__wrapper {
    @include grid;
}

.header__notice {
    background-color: $color-gallery;
    color: inherit;
    width: 100%;
    display: block;
    flex: 1 0 100%;
    padding: $measure-small;
    border-bottom-width: 0;
    position: relative;
    top: -$grid-padding;
    left: 50%;
    line-height: $line-height-small;
    text-align: center;
    transform: translateX(-50%);

    span {
        border-bottom: $border-width-hairline $border-style-default $transparent;

        &:after {
            content: ' ';
        }
    }

    &:hover,
    &:focus {
        span {
            border-bottom-color: currentColor;
        }
    }

    &:active {
        background-color: $color-black;
        color: contrasting-color($color-black);
    }
}

.header__logo {
    $header-logo-width: 140px;
    $header-logo-ratio: 0.469;
    @include grid-column-base;
    //@include hide-text;
    // @include grid-column(6, "small", true, false);
    // @include grid-column(3, "medium", true, false);

    align-self: center;
    padding-bottom: $measure-medium;

    @include media('<=between') {
        width: grid-column-width(6);
        padding: 1rem 0.5rem;
    }

    @include media('>between') {
        width: grid-column-width(3);
    }

    a,
    img {
        width: $header-logo-width;
        display: block;
    }

    a {
        @include anchor(reset);
        @include anchor(block);

        min-height: floor($header-logo-width * $header-logo-ratio);
    }

    @include media('<=small') {
        $mobile-logo-ratio: 0.8;
        a {
            min-height: floor(
                $mobile-logo-ratio * $header-logo-width * $header-logo-ratio
            );
        }
        img {
            width: floor($mobile-logo-ratio * $header-logo-width);
        }
    }
}

.header__toggles {
    text-align: right;
}

.breadcrumbs-dropdown {
    display: none;
}

.explore-theme {
    .header__logo {
        z-index: 2;
        a {
            @include anchor(tertiary-navigation--dark-bg);
        }
    }

    &:before {
        content: '';
        background-color: $color-black;
        display: block;
        height: 300px;
        width: 100vw;
        position: absolute;
        top: 0;
        overflow: hidden;
    }
}
