.course__kis__content a,
.course__kis__content a:active,
.course__kis__content a:focus,
.course__kis__content a:hover,
.course__kis__content a:link,
.course__kis__content a:visited {
    background-color: initial;
    border-bottom-color: initial;
    border-bottom-style: initial;
    border-bottom-width: initial;
    border-radius: initial;
    color: initial;
    display: initial;
    font-weight: initial;
    margin: initial;
    outline: initial;
    padding: initial;
    position: initial;
    text-align: initial;
    text-decoration: initial;
    transition: initial;
}

.kis-widget {
    margin-left: auto;
    margin-right: auto;

    h1,
    h3 {
        width: unset;
        flex: unset;
    }
}
