/*Transform mixin*/

.bayes-social-links{
    background-color: $color-home-social-bg;

    @include media('<=small'){
        overflow: hidden;
    }

    .bayes-full-width-container__wrapper{
        position: relative;
    }
}

.bayes-social-links__bg-image{
    display: none;
}

.bayes-social-links__icons{
    background-color: $color-home-social-header;
    color: $color-white;
    padding: $measure-medium;

    @include media('>large'){
        position: absolute;
        bottom: 3rem;
        left: 6rem;
        width: 80%;
    }

    @include media('<=large'){
        text-align: center;
    }

    @include media('>=small'){
        padding: 5rem;
    }

    h2{
        font-size: 2.75rem;
        line-height: 3rem;
    }
}

.bayes-social-links__instagram{
    display: flex;
    justify-content: flex-end;

    @include media('>large'){
        margin-bottom:3rem;
        margin-right: 3rem;
    }

    @include media('<=large'){
        justify-content: center;
    }

    .connect-social__card--insta{
        a{
            display: block;
        }
    }
}

.bayes-social-links__instagram__wrapper{

    .connect-social__card--insta{
        a{
            @include anchor-reset();
            @include media('>between','<extralarge'){
                width: 27rem;
                height: 27rem;
                overflow: hidden;
            }

            @include media('>=extralarge'){
                width: 33rem;
                height: 33rem;
                overflow: hidden;
            }
            
            &:active,
            &:focus{
                outline-color: $color-spotlight-focus;
                outline-offset: 1px;
                outline-width: $border-width-outline;
            }
        }
    }
}

.bayes-social-links__wrap{
    position: relative;
    @include media('>=between','<=large'){
        padding-top: 4rem;
        width: calc(100% - 8rem);
        left: 4rem;
    }

    @include media('<between'){
        padding: 1rem;
    }

    @include media('>large'){
        padding: 2rem 0;
    }
}
