.cass-knowledge-listing.landscape-knowledge-cards {
    @include media('>medium') {
        padding: 0 4rem;
    }
}

.landscape-knowledge-cards {

    .cass-knowledge-listing__grid.cass-knowledge-listing__grid--2 > div {
        padding-left: $measure-medium;
        padding-right: $measure-medium;

        @include media('>large') {
            margin-bottom: 8rem;
        }

        @include media('>extralarge') {
            margin-bottom: 5rem;
        }
    }

    .cass-knowledge-listing__item img {
        @include media('>extralarge') {
            width: 55%;
            max-width: 395px;
        }
    }

    .cass-knowledge-listing__item__text {
        @include media('>extralarge') {
            width: 70%;
        }

        
    }

    div.item__date {
        margin: 1.5rem 0;
    }
}

.knowledge-article__link-container {
    margin-top: 0.5rem;
}

.cass-knowledge {
    .news-article__article-meta-data {
        color: $color-dove-gray;
    }

    .cass-knowledge-attachment {
        padding: 1.5rem;
        border: 0.25rem solid $color-alizarin;
        margin-bottom: 3rem;

        h2 {
            font-size: 1.44rem;
        }

        .icon {
            float: left;
            margin-top: 0.25rem;
            margin-right: $measure-medium;
        }
    }
}

.cass-knowledge-author__title {
    margin: 0.5rem 0;
    font-weight: 600;
    display: block;
}

.cass-knowledge-author__external {
    margin-left: $measure-medium;
}

.cass-knowledge-author {
    ul {
        margin-bottom: 0;
    }

    li {
        list-style-type: disc;
        margin-bottom: 0.2rem;
    }
}

.knowledge-article__author {
    ul {
        margin-left: $measure-large;
    }
}

.related-research-papers-section {
    .related-research-paper {
        border-top: $border-width-hairline solid $color-nobel;
        border-bottom: $border-width-hairline solid $color-nobel;
    }
}

.news-navigation {
    display: none;
}

.news-article__press-body {
    blockquote {
        &:before {
            background-color: $color-alizarin;
        }
    }
}

.related-stories-section {
    h2 {
        margin-bottom: $measure-enormous;
    }
}

.cass-knowledge-listing__item__research,
.cass-knowledge-listing__item__text > a {
    margin-bottom: $measure-medium;
}
