.bayes-programmes{
    padding-top: 5rem;
    padding-bottom: 5rem;
    margin-top: 0;
    background-color: rgba(0, 0, 0, 0.7);
    position: relative;
    overflow: hidden;
}

.bayes-programmes--bluebg{
    background-color: $color-prussian;
}

.bayes-programmes--image-bg{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    width: 100%;

    img{
        width: 100%;
    }

    &:after{
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.8);
    }

    @include media('<medium'){
        top: 0;
        transform: initial;
        height: 100%;

        img{
            height: 100%;
        }
    }
}

.bayes-programmes--title{
    font-size: 3.75rem;
    color: $color-white;
    line-height: 1.08;
}

.bayes-programmes__list{
    margin-top: 3rem;
    font-size: 1.5rem;
    font-weight: 500;
    color: $color-white;

    @include media('>between'){
        display: flex;
        justify-content: flex-end;
    }

     ul{
        list-style: none;
        margin-left: 0;
        margin-bottom: 0;

        @include media('>between'){
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            column-gap: 1.5rem;
            row-gap: 0.5rem;
            width: grid-column-width(8);
        }

        @include media('<between'){
            padding-bottom: 2rem;
        }

        li{
            margin-top: 0;
            a{
                @include anchor-reset();
                display: flex;
                justify-content: space-between;
                align-items: center;
                border-bottom: $border-width-hairline solid rgba(255, 255, 255, 0.3);
                transition: all 0.3s;
    
                &:hover{
                    border-bottom-color: rgb(255, 255, 255);
                }

                &:focus{
                    border-bottom-color: rgb(255, 255, 255);
                    outline: $border-width-outline $border-style-default $color-white;
                    outline-offset: 3px;
                }

                &:active{
                    border-bottom-color: rgb(255, 255, 255);
                    outline: $border-width-outline $border-style-alternate $color-white;
                    outline-offset: 3px;
                }
            }
        }

        span{
            font-size: $measure-medium;
            font-weight: 500;
        }
     }
}