////
/// MBA popup
/// @group components
////

.mba-popup {
    align-items: center;
    background: rgba($color-black, 0.7);
    display: none !important;
    height: 100%;
    justify-content: center;
    left: 0;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: z-index(modal);

    h2 {
        max-width: 70%;
    }
}

.mba-popup.open {
    display: flex !important;
}

.mba-popup__form {
    background-color: $color-white;
    display: inline-block;
    flex: auto;
    height: auto;
    max-height: calc(100% - #{$measure-medium});
    max-width: calc(100% - #{$measure-medium});
    overflow-y: auto;
    position: relative;
    width: auto;

    @include media('>medium') {
        max-height: 70%;
        max-width: 50%;
    }

    @include media('>small') {
        padding: $measure-large;
    }

    @include media('<=small') {
        padding: $measure-large $measure-medium $measure-large;
    }
}

.mba-popup__form__close {
    position: absolute;

    @include media('>small') {
        right: $measure-large;
        top: $measure-large;
    }

    @include media('<=small') {
        right: $measure-medium;
        top: $measure-large;
    }

    @include anchor(cta--red);
}

.mba-popup__form__close__icon {
    @extend .fa;
    @extend .fa-close;
}
