.bayes-statistic{
    position: relative;
    padding-top: 5rem;
    padding-bottom: 5rem;
    background-color: $color-home-stats;
    color: $color-white;

    &:before{
        content: "";
        width: 30%;
        height: 4rem;
        background-color: $color-home-stats;
        position: absolute;
        top: -4rem;
        left: 0;
    }

    @include media('<=between'){
        padding-bottom: 2rem;
        padding-top: 2rem;
    }
}

.bayes-statistic__wrapper{
    width: 100%;

    h2{
        font-size: 2rem;
        font-weight: bold;
        line-height: 1.56;
    }
}

.bayes-statistic__block{
    @include media('>between'){
        display: flex;
        justify-content: start;
    }

    li{
        display: flex;
        justify-content: start;
        align-items: flex-start;

        @include media('>between'){
            width: 33%;
        }

        @include media('<=between'){
            padding-bottom: $measure-medium;
        }
    }
}

.bayes-statistic__block__icon{
    margin-right: $measure-medium;
    border: $border-width-hairline solid $color-white;

    &:before{
        display: block;
        padding: 0.5rem;
    }
}

.bayes-statistic__block__text{
    display: flex;
    flex-direction: column;
}

.bayes-statistic__block__text--title{
    @include font-family(meta);
    font-weight: $font-weight-medium;
    line-height: 1;
    font-size: 1.25rem;
}

.bayes-statistic__block__text--description{
    p{
        margin-bottom: unset;
    }
    
    a{
        @include anchor-reset();
        border-bottom: $border-width-hairline solid $color-white;

        &:focus{
            outline: $border-width-outline $border-style-default $color-white;
        }.bayes-statistic__block

        &:active{
            outline: $border-width-outline $border-style-alternate $color-white;
        }
    }
}