/*SVG controls styles*/

.bayes-home{
    .video-banner__controls{
        right: 5rem;
        bottom: 5rem;

        @include media('>large'){
            right: 10rem;
        }
    }

    .video-banner__play,
    .yt-video__cta {
        position: absolute;
        background-color: transparent;
        z-index: 2;
        border-bottom: none;
        width: initial;
        background-color: initial;
        background: initial;

        &:focus,
        &:active{
            outline-color: $color-white; 
            outline-offset: 1px;
        }

        &:hover,
        &:active {
            svg{
                .stroke-dotted {
                    stroke-width: 4px;
                    opacity: 1;
                }
    
                .stroke-solid {
                    opacity: 0;
                    stroke-dashoffset: 200;
                }
    
                .circle {
                    fill: $color-black;
                    fill-opacity: 0;
                }
            }
        }

        &:active,
        &:hover,
        &:focus{
            background-color: transparent;
        }
    
        svg {
            max-width: initial;
            width: 50px;
            height: 50px;
            top: 35%;
            fill: none;
            margin: 0 auto;
    
            @include media('<small') {
                width: 80px;
                height: 80px;
            }
    
            .stroke-dotted {
                opacity: 0;
                stroke-dasharray: 1, 8;
                stroke-width: 4px;
                stroke-linecap: round;
                transform-origin: 50% 50%;
                animation: spin 20s infinite linear;
                transition: opacity 1s ease, stroke-width 1s ease;
                stroke: $color-white;
            }
    
            .stroke-solid {
                stroke-dashoffset: 0;
                stroke-dasharray: 300;
                stroke-width: 4px;
                transition: stroke-dashoffset 1s ease, opacity 1s ease;
                stroke: $color-white;
            }
    
            .icon {
                transform: translate(-16px, -16px) scale(0.025);
                transform-origin: 50% 50%;
                //transition: transform 200ms ease-out;
                fill: $color-white;
            }
    
            .circle {
                fill: $color-black;
                fill-opacity: 0.2;
            }
        }
    }

    .yt-video__cta{
        left: 50%;
        transform: translateX(-50%);
        svg{
            .icon {
                transform: scale(1);
            }
        }
    }

    .video-banner__play.video-banner__play--pause{
        svg{
            .icon{
                transform: scale(1);
            }
        }
    }
}