@mixin filter-field($active-bg: true) {
    font-size: 16px;

    padding: 11px;
    height: 45px;
    border: 1px solid $color-nobel;
    border-radius: 0;
    background: $color-white;

    @if $active-bg {
        &:hover,
        &:focus,
        &:active {
            background: $color-gallery;
        }
    }
}

/******* mobile floating filters ***********/

.filter-box {
    position: relative;
}

.filter-box__open {
    display: block;
    padding: 5px 10px;

    border: 1px solid;

    &:link,
    &:visited {
        background: $color-alizarin;
        color: $color-white;
        border-color: $color-alizarin;
    }

    &:hover {
        background: $color-alizarin;
        color: $color-white;
        border-color: $color-alizarin;
    }

    &:active,
    &:focus {
        background: $color-white;
        color: darken($color-alizarin, 6%);
        border-color: darken($color-alizarin, 6%);
    }

    @include media('>between') {
        display: none;
    }

    h2 {
        margin: 0;
    }

    h2 {
        &,
        span,
        i {
            line-height: 40px;
        }
    }

    h3 {
        &,
        span,
        i {
            line-height: 29px;
        }
    }

    .fa {
        float: right;
    }
}

.filter-box__float {
    overflow: hidden;
    height: 0;
    margin-bottom: 20px;

    &.filter-box__float--open,
    &.filter-box__float--fixed {
        height: auto;
    }

    @include media('>between') {
        &,
        &.filter-box__float--open,
        &.filter-box__float--fixed {
            height: auto;
        }
    }
}

.filter-box__inner {
    border: solid $color-gallery;
    border-width: 0 1px 1px;
    padding: 20px 20px 0;
    background: white;

    h2 {
        display: none;
    }

    @include media('>between') {
        padding: 10px 20px;
        border: none;
        h2 {
            display: block;
        }
    }
}

.filter-box__float--fixed .filter-box__inner {
    padding-top: 10px;
    h2 {
        display: block;
    }
}

.filter-box__filter {
    margin-bottom: 20px;
}

.filter-box__filter__header a {
    display: block;
}

.filter-box__filter__header .fa {
    display: none;
}

.js {
    .filter-box__filter__header {
        cursor: pointer;
    }

    .filter-box__filter__header .fa {
        display: block;
        float: right;
    }

    .filter-box__filter__content--closed {
        display: none;
    }
}

.filter-box__filter__content {
    label {
        display: block;
        cursor: pointer;
    }

    select {
        width: 100%;

        @include filter-field;
    }

    input {
        position: relative;
        top: 4px;

        &[type='checkbox'] {
            top: 0;
        }
    }
}
