////
/// Video.video-banner Base
/// @group components
////

.youtube-embed-container {
    display: none;
    width: 95%;
    max-width: 1408px;
    margin: auto;
    position: relative;
    
    iframe {
        display: block;
    }
}

.image-banner {
    color: white;

    @include media('<=between') {
        display: block;
        height: 400px;
        background: rgb(0, 0, 0);
        &:after {
            content: '';
            background: url('https://www.bayes.city.ac.uk/__data/assets/image/0003/631785/Bayes-Business-School,-changing-our-name.jpg')
                no-repeat;
            top: 0;
            left: 0;
            bottom: 0;
            right: 0;
            position: absolute;
            z-index: 1;
            max-height: 500px;
        }
    }
    @include media('>between') {
        display: none;
    }

    span {
        @extend .fa;
        @extend .fa-play;
        margin: 5px 7px;
    }

    .image-banner__text__wrapper {
        top: 15%;
        position: relative;
        z-index: 2;

        h2 {
            text-align: center;
            padding: 10px;
            font-weight: 300;
        }

        .video-cta {
            display: block;
            border-bottom-width: 0;
            background-color: white;
            color: #1e1e1e;
            padding: 10px 20px;
            font-size: 16px;
            font-weight: 600;
            margin: auto;
            width: 60%;
            text-align: center;
            &:hover {
                background-color: #ececec;
                color: #1e1e1e;
                cursor: pointer;
            }
            &:active {
                outline: 2px dotted #1e1e1e;
            }
            &:focus {
                outline: 3px solid #1e1e1e;
            }
        }
    }
}

.promo-cta h2 {
    padding: 0;
}

.section--lightgray.promo-card__wrapper {
    @include media('<=between') {
        margin-top: -50px;
    }
    padding-bottom: 40px;
    @include media('<=between') {
        padding-bottom: 10px;
        margin-bottom: 20px;
    }
    .left-box {
        @include media('<=between') {
            width: 100%;
            margin: 0 auto;
            z-index: 2;
            padding-top: 0.5rem;
            background-image: linear-gradient(
                180deg,
                rgba(0, 0, 0, 0) 15%,
                #f2f2f2 0
            );
            .grid__6:first-of-type {
                // margin-top: -50px;
            }
            .promo-cta {
                padding: 10px 15px;
            }
        }
        @include media('>small') {
            .grid__6:first-of-type {
                margin-top: 0;
            }
        }
    }
}

.video-banner {
    list-style-type: none;
    margin-left: 0;
    margin-top: 0;
    position: relative;
}

.video-banner__banner {
    display: none;
    position: relative;

    &:before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        z-index: z-index(default);
        background-color: rgba(0, 0, 0, 0.5);
        opacity: 0;
        width: 100%;
        height: 96%; //refactor to remove magic numbers
        transition: opacity 1s;

        @include media('>small') {
            height: 98%; //refactor to remove magic numbers
        }

        @include media('>medium') {
            height: 99%; //refactor to remove magic numbers
        }
    }

    .video-banner__text__wrapper {
        opacity: 0;
        transition: opacity 2s;
    }
}

.video-banner__banner.playing {
    display: block;

    &.text,
    &.textout {
        &:before {
            opacity: 1;
        }
    }

    &.text .video-banner__text__wrapper {
        opacity: 1;
    }

    @include media('<=between') {
        display: none;
    }
    @include media('>between') {
        display: block;
    }
}

.video-banner__text__wrapper {
    position: absolute;
    text-align: center;
    top: 0;
    width: 100%;
    z-index: z-index(above);
    display: flex;
    height: auto;
    margin-top: 16%;

    .video-banner__text {
        @include transition(
            opacity,
            $transition-duration-long,
            $transition-function
        );

        display: block;
        margin: auto;
        width: 60%;

        @include media('>medium', '<=large') {
            bottom: 25px;
        }

        .video-banner__text__title,
        .video- {
            @extend %alpha;
            @include font-family(meta);
            color: $color-lightgrey;
            font-size: $font-size-alpha;
            font-weight: $font-weight-default;
            line-height: $line-height-default;
            // margin-bottom: $measure-large;
            // margin-top: (-$measure-medium);
            margin: auto;
            text-shadow: 0 0 $border-width-hairline $color-black;
        }

        .video-banner__text__title {
            margin-bottom: 1rem;
            display: inline;

            @include media('>large') {
                // margin-top: 12%;
                margin: auto;
            }

            @include media('>medium', '<=large') {
                // margin-top: 5%;
                margin: auto;
            }

            @include media('<=medium') {
                // margin-top: 14%;
                font-size: $font-size-beta;
            }

            @include media('<=small') {
                font-size: $font-size-delta;
            }

            @include media('<=min') {
                font-size: $font-size-default;
            }
        }

        .video-banner__text__subtitle {
            font-size: $font-size-beta;

            @include media('<=medium') {
                font-size: $font-size-gamma;
                line-height: $line-height-small;
            }

            @include media('<=small') {
                font-size: $font-size-default;
            }
        }
    }
}

.video-banner__controls {
    display: inline-block;
    position: absolute;
    z-index: z-index(above);

    right: $measure-medium;
    bottom: $measure-medium;

    @include media('>medium') {
        right: $measure-large;
        bottom: $measure-large;
    }
}

.video-banner__play {
    @include anchor('cta');

    @include media('<=between') {
        display: none;
    }

    border: 0;
    background: white;

    padding: 0.25rem 0;
    width: 2rem;
    text-align: center;
    display: inline-block;

    span {
        @extend .fa;
        @extend .fa-pause;
    }

    &.video-banner__play--pause span {
        @extend .fa-play;
    }
}