////
/// Bracket Block
/// @group components
////

.bracket-block {
    width: 100%;
    position: relative;
    @include clear;
}

.banner-bracket-block__anglesA,
.banner-bracket-block__anglesB,
.profile-bracket-block__anglesA,
.profile-bracket-block__anglesB,
.bayes-news__title--bracket-angleA,
.bayes-news__title--bracket-angleB,
.bayes-feature-content__img-text--bracket-angleA,
.bayes-feature-content__img-text--bracket-angleB,
.subject-bracket-block__anglesA,
.subject-bracket-block__anglesB {
    &:after,
    &:before {
        content: ' ';
        position: absolute;
        width: $measure-medium;
        height: $measure-medium;
        border-style: $border-style-default;
        border-color: $color-alizarin;
    }
}

.banner-bracket-block__anglesA,
.banner-bracket-block__anglesB,
.bayes-news__title--bracket-angleA,
.bayes-news__title--bracket-angleB,
.subject-bracket-block__anglesA,
.subject-bracket-block__anglesB {
    &:after,
    &:before {
        height: 100%;
    }
}

.banner-bracket-block__anglesA,
.profile-bracket-block__anglesA,
.bayes-news__title--bracket-angleA,
.subject-bracket-block__anglesA{
    &:before {
        top: 0;
        left: 0;
        border-width: $measure-small 0 0 $measure-small;
    }

    &:after {
        bottom: 0;
        left: 0;
        border-width: 0 0 $measure-small $measure-small;
    }
}

.bayes-feature-content__img-text--bracket-angleA {
    &:before {
        top: -$measure-smallish;
        left: -$measure-smallish;
        border-width: $measure-smallish 0 0 $measure-smallish;
    }
}

.banner-bracket-block__anglesB,
.profile-bracket-block__anglesB,
.bayes-news__title--bracket-angleB,
.subject-bracket-block__anglesB {
    &:before {
        top: 0;
        right: 0;
        border-width: $measure-small $measure-small 0 0;
    }

    &:after {
        bottom: 0;
        right: 0;
        border-width: 0 $measure-small $measure-small 0;
    }
}

.bayes-feature-content__img-text--bracket-angleB{
    &:after {
        bottom: -$measure-smallish;
        right: -$measure-smallish;
        border-width: 0 $measure-smallish $measure-smallish 0;
    }
}

.banner-bracket-block__anglesA,
.banner-bracket-block__anglesB {
    &:before {
        top: -$grid-padding;
    }
}

.banner-bracket-block__anglesA,
.banner-bracket-block__anglesB {
    &:after {
        top: $grid-padding;
    }
}

.banner-bracket-block__anglesA {
    &:before,
    &:after {
        left: -$grid-padding;
    }
}

.banner-bracket-block__anglesB {
    &:before,
    &:after {
        right: -$grid-padding;
    }
}

.banner-bracket-block,
.profile-bracket-block,
.subject-bracket-block {
    @extend .bracket-block;

    margin-top: $measure-large;
    margin-bottom: $measure-large;
}

.profile-bracket-block__img {
    @include grid-column(4, 'min');
    align-self: center;

    @include media('>medium', '<large') {
        @include grid-column(3, 'min');
    }

    @include media('>large') {
        @include grid-column(2, 'min');
    }
}

.profile-bracket-block__text {
    @include grid-column(8, 'min');
    align-self: center;

    @include media('>medium', '<large') {
        @include grid-column(9, 'min');
    }

    @include media('>large') {
        @include grid-column(10, 'min');
    }
}

.profile-bracket-block__text__name {
    @include font-family(meta);
    font-size: $font-size-delta;
    font-weight: $font-weight-light;

    @include media('>medium') {
        font-size: $font-size-gamma;
    }
}

.profile-bracket-block__item {
    @include grid;
    padding: $measure-medium;
    position: relative;
}

.profile-bracket-block__item:not(:last-child) {
    margin-bottom: $measure-large;
}

.bayes-feature-content__img-text--bracket-angleA,
.bayes-feature-content__img-text--bracket-angleB{
    &:before,
    &:after{
        width: $measure-large;
        height: $measure-large;
    }
}