@keyframes fade-up {
    from {
        opacity: 0;
        transform: translateY(0);
    }

    to {
        opacity: 1;
        transform: translateY(-2rem);
    }
}

.carousel {
    position: relative;
}

.carousel__control {
    position: absolute;
    top: 35%;
    right: 0;
    width: 30px;
    z-index: 2;
}

.carousel__control__button {
    a.carousel__control__button__link {
        display: block;
        border-radius: 50%;
        width: 16px;
        height: 16px;
        margin-bottom: 24px;
        cursor: pointer;
        &:before {
            content: '';
            position: absolute;
            left: -6px;
            top: -6px;
            transition: all 0.2s ease;
            border-radius: 50%;
            bottom: 0;
            width: 28px;
            height: 28px;
        }
        @include anchor(control-button);
    }
}

.carousel__item {
    position: relative;
    display: none;
}

.carousel__item.active {
    display: block;
    animation: fade-up 1s ease-in forwards;
}

.carousel__item__info {
    position: absolute;
    top: 0;
    left: 0;
    max-width: 300px;
}

.carousel__item__info__overline {
    text-transform: uppercase;
    color: $color-white;
    font-weight: $font-weight-bold;
    animation: fade-up 0.5s ease-in forwards;

    &:after {
        content: '';
        height: 3px;
        width: 80px;
        display: inline-block;
        background-color: $color-nobel;
        position: relative;
        bottom: 3px;
        margin-left: $measure-small;
    }
}

.carousel__item__info__body {
    color: white;
    font-size: $font-size-alpha;
    @include font-family(meta);
    font-weight: $font-weight-bold;
    margin-top: $measure-medium;
    animation: fade-up 1s ease-in forwards;
    @include media('>medium') {
        font-size: 3rem;
    }
}

.carousel__item__info__cta {
    @include anchor(cta);
    @include anchor(cta--black);
    text-align: left;
    padding: $measure-medium;
    margin-top: $measure-large;
    margin-left: $measure-small;
    animation: fade-up 1.5s ease-in forwards;
    span {
        position: absolute;
        right: $measure-small;
        top: 1.2rem;
    }
}

.explore-theme {
    .carousel__item__info {
        top: $measure-large;
        padding-top: $measure-large;

        @include media('>medium') {
            top: 5rem;
            padding-top: 5rem;
        }
    }

    .carousel__item {
        top: 22px;
        min-height: 320px;

        @include media('>medium') {
            top: 0;
        }
    }
    .carousel {
        max-height: 800px;
        overflow: hidden;
    }
}

.no-js {
    div[data-card='1'] {
        display: block;
    }

    .carousel__control {
        display: none;
    }
}
