.bayes-youtube-embed-container {
    display: none;
    max-width: 1408px;
    margin: auto;
    position: relative;
    padding: 3rem;
    
    iframe {
        display: block;
    }


    .youtube-embed-container__close-btn{
        position: absolute;
        color: $color-white;
        top: 0.5rem;
        right: 1rem;

        &:active,
        &:focus{
            outline-color: $color-white
        }
    }
}

.bayes-video-banner {
    list-style-type: none;
    margin-left: 0;
    margin-top: 0;
    position: relative;
}

.bayes-video-banner__banner {
    display: none;
    position: relative;

    &:before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        z-index: z-index(default);
        background-color: rgba(0, 0, 0, 0.5);
        opacity: 0;
        width: 100%;
        height: 96%; //refactor to remove magic numbers
        transition: opacity 1s;

        @include media('>small') {
            height: 98%; //refactor to remove magic numbers
        }

        @include media('>medium') {
            height: 99%; //refactor to remove magic numbers
        }
    }

    .bayes-video-banner__text__wrapper {
        opacity: 0;
    }
}

.bayes-video-banner__banner.playing {
    display: block;

    &.text,
    &.textout {
        &:before {
            opacity: 1;
            transition: opacity ease-in 3s;
        }
    }

    &.text .bayes-video-banner__text__wrapper {
        opacity: 1;
    }

    @include media('<=between') {
        display: none;
    }
    @include media('>between') {
        display: block;
    }
}

.bayes-video-banner__text__wrapper {
    position: absolute;
    top: 0;
    z-index: z-index(above);
    display: flex;
    height: auto;
    margin-top: 16%;

    .video-banner__text {
        @include transition(
            opacity,
            $transition-duration-long,
            $transition-function
        );

        display: block;
        margin: auto;

        @include media('<medium'){
            width: 60%;
        }
        

        @include media('>medium', '<=large') {
            bottom: 25px;
        }

        .video-banner__text__title,
        .video- {
            @extend %alpha;
            @include font-family(meta);
            color: $color-lightgrey;
            font-size: $font-size-bayes-banner;
            font-weight: $font-weight-bold;
            line-height: $line-height-default;
            // margin-bottom: $measure-large;
            // margin-top: (-$measure-medium);
            margin: auto;
            text-shadow: 0 0 $border-width-hairline $color-black;
        }

        .video-banner__text__title {
            margin-bottom: 1rem;
            display: inline;

            @include media('>large') {
                // margin-top: 12%;
                margin: auto;
            }

            @include media('>medium', '<=large') {
                // margin-top: 5%;
                font-size: $font-size-omicron;
                margin: auto;
            }

            @include media('<=medium') {
                // margin-top: 14%;
                font-size: $font-size-beta;
            }

            @include media('<=small') {
                font-size: $font-size-delta;
            }

            @include media('<=min') {
                font-size: $font-size-default;
            }
        }

        .video-banner__text__subtitle {
            font-size: $font-size-beta;

            @include media('<=medium') {
                font-size: $font-size-gamma;
                line-height: $line-height-small;
            }

            @include media('<=small') {
                font-size: $font-size-default;
            }
        }
    }
}