.course-subject-list{
    padding: 1rem 2rem;

    @include media('<between'){
        h2{
            line-height: 1.1;
        }
    }

    @include media('<=small'){
        padding: 1rem;
    }

    .subject-bracket-block__anglesA,
    .subject-bracket-block__anglesB {
        &:before,
        &:after{
            width: $measure-large;

            @include media('<=medium'){
                width: 1.5rem;
            }

            @include media('<=small'){
                width: 1rem;
            }
        }
    }

    .subject-bracket-block__anglesA{
        &:before {
            border-width: $measure-medium 0 0 $measure-medium;

            @include media('<=medium'){
                border-width: 0.75rem 0 0 0.75rem;
            }

            @include media('<=small'){
                border-width: $measure-small 0 0 $measure-small;
            }
        }
        &:after{
            border-width: 0 0 $measure-medium $measure-medium;

            @include media('<=medium'){
                border-width: 0 0 0.75rem 0.75rem;
            }

            @include media('<=small'){
                border-width: 0 0 $measure-small $measure-small;
            }
        }
    }

    .subject-bracket-block__anglesB{
        &:before{
            border-width: $measure-medium $measure-medium 0 0;

            @include media('<=medium'){
                border-width: 0.75rem 0.75rem 0 0;
            }

            @include media('<=small'){
                border-width: $measure-small $measure-small 0 0;
            }
        }
        &:after{
            border-width: 0 $measure-medium $measure-medium 0;

            @include media('<=medium'){
                border-width: 0 0.75rem 0.75rem 0;
            }

            @include media('<=small'){
                border-width: 0 $measure-small $measure-small 0;
            }
        }
    }

    ul{
        list-style: none;
        margin-bottom: initial;
        margin-left: initial;

        @include media('>between'){
            margin-bottom: 0.5rem;
        }

        @include media('>small'){
            column-count: 2;
            column-gap: 1.5rem;
        }
    }
}

.course-subject-list-undergraduate{
    .subject-bracket-block__anglesA,
    .subject-bracket-block__anglesB{
        &:before,
        &:after{
            border-color: $color-flamingo;
        }
    }
}

