////
/// Secondary Navigation
/// @group components
////

.secondary-navigation {
    @include grid-base;
    background-color: $color-lightgray;
    display: inline-block;
    box-shadow: inset 0 0px 8px 0 rgba(0, 0, 0, 0.15);
    z-index: 1;

    @include media('<=extralarge') {
        width: calc(100% + #{$grid-padding * 2});
        margin-left: -$grid-padding;
        margin-right: -$grid-padding;
    }

    @include media('<=medium') {
        display: none;
    }

    &:before {
        content: '';
        background: $color-lightgray;
        width: 200vw;
        height: 100%;
        position: absolute;
        transform: translateX(-50%);
        box-shadow: inset 0 0px 8px 0 rgba(0, 0, 0, 0.15);
    }
}

.secondary-navigation__list {
    @include grid-column(12);
    @include reset-font-size('li');
    text-align: center;

    li {
        display: inline-block;
    }

    a {
        @include anchor(secondary-navigation);
    }
}

.secondary-navigation__list--home {
    i {
        position: relative;
        top: 5px;
    }
    &:hover{
        i {
            top: 0px;
        }
    }
}
