////
/// URL Dropdown
/// @group components
////

.url-dropdown select {
    padding: $measure-small $measure-medium;

    .no-js & {
        @include visually-hidden;
        border: thick solid red;
    }
}

// date drop down styling for undergraduate course pages

.url-dropdown--ug {
    @include clear-dropdown-menu;
    position: relative;
    
    select {
        @include font-family(meta);
        @include anchor('dropdown');
        font-weight: $font-weight-bold;
        font-size: $font-size-default;
        padding-right: $measure-large;
    }

    // for no JS:
    a {
        display: block;
    }
}
