.youtube-embed--for-content {
    position: relative;
    margin: $measure-medium 0;
    width: 100%;

    @include media('<small') {
        min-height: 215px;
    }

    iframe {
        display: block;
    }
}

.cookieconsent-optout-marketing {
    display: block;
    color: $color-white;
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    z-index: 1;

    img {
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        z-index: 1;
    }
}

.cookieconsent-optout-marketing__inner {
    padding: $measure-medium;
    background-color: $color-dove-gray;
    position: relative;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 100%;
    width: 100%;
    border: $border-width-default solid $color-alizarin;
}

.cookieconsent-optout-marketing__inner__content {
    z-index: 2;

    a {
        @include anchor('white');
    }

    p {
        @include media('<small') {
            margin-bottom: 0.5rem;
        }
    }

    i {
        color: $color-white;
        font-size: 2rem;
        padding: $measure-medium;

        &:before {
            padding: 0.75rem;
            border: $border-width-default $border-style-default;
            border-radius: 100%;
        }
    }
}

/// cookie vendor override
#CybotCookiebotDialog {
    #CybotCookiebotDialogBodyLevelButtonLevelOptinAllowallSelectionWrapper {
        .CybotCookiebotDialogBodyButton {
            padding-top: $measure-small;
            padding-bottom: $measure-small;
        }
    }

    #CybotCookiebotDialogBodyLevelButtonLevelOptinDeclineAll,
    #CybotCookiebotDialogBodyLevelButtonLevelOptinAllowallSelection {
        border: 1px solid transparent;
        color: $color-red;
        background-color: transparent;
        transition: border $transition-duration;

        &:hover {
            border: 1px solid $color-red;
        }

        &:focus,
        &:active {
            border: 1px solid $color-red;
        }
    }

    #CybotCookiebotDialogBodyLevelButtonLevelOptinAllowAll {
        background-color: $color-red;
        transition: background-color $transition-duration;
        border: 2px dotted transparent;

        &:hover {
            background-color: darken($color-red, 10%);
            border: 2px dotted transparent;
        }

        &:focus,
        &:active {
            border: 2px dotted $color-white;
        }
    }
}
