.banner__content__video__grid {
    @include grid;
}

.banner--video {
    // padding-top: $measure-large;
    padding-bottom: $measure-large;

    @include media('<=medium') {
        padding-top: $measure-enormous;
    }

    &:not(.banner--video--imgbg) {
        background: gradient(0deg, #1e1e1e, #1e1e1e) no-repeat;
        background-size: 100% 60%;
    }

    .banner__imgbg {
        position: absolute;
        top: 0;
        left: 0;
        min-width: 100%;
        width: auto;
        height: 60%;
        overflow: hidden;

        img {
            min-width: 100%;
            min-height: 100%;
            width: auto;
            height: auto;
        }

        &:after {
            display: block;
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: rgba(0, 0, 0, 0.9);
        }
    }

    .banner__content__ctas {
        margin-top: $measure-enormous;
        a {
            @include anchor(cta);
            @include anchor(cta--inline);
            @include anchor(cta--allports);

            margin: 0 $measure-large $measure-large 0;
        }
    }

    .banner__content .banner__content__text {
        @include media('<=medium') {
            // margin-top: $measure-large;
            margin-bottom: $measure-medium;
            padding-top: 0;
        }
    }
}

.banner__content__video {
    @include grid-column(6);

    padding-top: 0;

    @include media('>medium') {
        padding-top: $measure-large;
    }
}
