////
/// Buttons
/// @group base
////

a.button {
    display: inline-block;
}

button,
.button {
    @extend %button-elements !optional;
    padding: $measure-small-relative;

    &:focus {
        outline: $border-width-outline $border-style-default $color-black;
    }

    &:active {
        outline: $border-width-default $border-style-alternate $color-black;
    }
}

// TODO: Change these into variables
.button--icon {
    color: white;
    background: #d92231;

    &:hover {
        background: #be1e2b;
    }

    span {
        background: transparent;
    }
}
